.accordion {
  padding-top: 60px;
  padding-bottom: 30px;
  position: relative;

  &::after {
    background: rgba(black, 0.5);
    content: "";
    display: block;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    visibility: hidden;
    width: 100%;
    height: 100%;
    z-index: 1;
      @media (min-width: 1025px) {
        transition: all 0.4s $cubic;      
      }
  }

  &--overlay {
    &::after {
      opacity: 1;
      visibility: visible;
        @media (max-width: 1024px) {
          position: fixed;
          z-index: 10002;
          height: 100vh;
        }
    }
  }

  &__container {
    @include make-container;
  }

  &__item {
    border-bottom: 1px solid rgba(black, 0.3);

    &--active {
      .accordion__icon::after {
        opacity: 0;
        transform: rotate(90deg);
      }
    }
  }
  
  &__title {
    align-items: center;
    cursor: pointer;
    display: flex;
    font-family: 'Adobe Garamond Pro';
    font-size: 40px;
    font-weight: 400;
    line-height: 1.35;
    letter-spacing: 0;
    padding: 40px 12px 40px 0;
    justify-content: space-between;

    @media (max-width: 1024px) {
      font-size: 32px;  
      padding: 27px 12px 27px 0;
    }
  }
  &__body.default-active-height{
    height: 591px;
    @media (max-width: 1024px) {
      height: 285px;
    }
  }
  &__body.default-active-height-single{
    height: 518px;
    @media (max-width: 1024px) {
      height: 572px;
    }
  }
  &__text {
    align-items: center;
    color: $color--dark;
    display: flex;

    @media (max-width: 1024px) {
      font-family: 'Avenir LT Pro';
      font-size: 14px;
      font-weight: 700;
      line-height: 16px;
      letter-spacing: 1.17px;
      text-transform: uppercase;
    }
  }

  &__image {
    margin-right: 8px;
    margin-left: 17px;
    width: 50px;
    height: 50px;
  }
  
  &__paragraph {
    flex: 0 0 calc(100% - 60px);
    font-size: 14px;
    font-weight: 700;
    line-height: 24px;
    letter-spacing: 0;
    margin: 0;
  }
  
  &__icon {
    align-items: center;
    display: flex;
    flex: 0 0 25px;
    justify-content: center;
    position: relative;
    width: 25px;
    height: 25px;
  
    &::before {
      background: $color--black;
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 25px;
      height: 2px;
    }

    &::after {
      background: $color--black;
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transition: all 0.4s $cubic;
      transform: translate(-50%, -50%);
      transform-origin: 7px 7px;
      width: 2px;
      height: 25px;
    }

    &--small {
      &::before {
        width: 11px;
        height: 2px;
      }

      &::after {
        width: 2px;
        height: 11px;
      }
    }
  }
  
  &__body {
    transition: all 0.4s $cubic;
    overflow: hidden;
    height: 0;

    &-wrapper {
      align-items: flex-start;
      display: flex;
      flex-wrap: wrap;
      padding-bottom: 50px;

      @media (max-width: 1024px) {
        padding-bottom: 30px;  
      }
    }

    &-copy {
      flex: 0 0 42.5%;
      padding-right: 27px;

      @media (max-width: 1024px) {
        flex: 0 0 100%;  
        padding-right: 0;
      }
    }

    h3 {
      font-family: 'Avenir LT Pro';
      font-size: 18px;
      font-weight: 700;
      line-height: 1.66;
      letter-spacing: 0.45px;
      margin-bottom: 0;

      & + p {
        font-weight: 400;
        letter-spacing: 0.4px;
        line-height: 1.625;
        margin-top: 10px;

        & + h3 {
          margin-top: 35px;

          @media (max-width: 1024px) {
            margin-top: 20px;  
          }
        }
      }
    }

    .video-wrapper {
      flex: 1;
      position: relative;

      @media (max-width: 1024px) {
        flex: 0 0 100%;
        margin-bottom: 20px;
      }

      &::before {
        content: "";
        display: block;
        padding-bottom: 56.25%; 
      }

      iframe {
        position: absolute; 
        top: 0; 
        left: 0; 
        width: 100%; 
        height: 100%; 
      }
    }
  }

  &__list--mob {
    .accordion__item {
      background: $color--gray;
      border-radius: 4px;
      border-top: 0;
      border-bottom: 0;

      & + * {
        margin-top: 20px;
      }
    }

    .accordion__title {
      padding: 10px 12px 10px 0;
    }
  }
}

.time-box {
  display: flex;
  margin-bottom: 35px;

  @media (max-width: 1024px) {
    justify-content: space-between;  
  }

  &__item {
    align-items: center;
    background: $color--gray;
    border-radius: 8px;
    color: $color--dark;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    justify-content: center;
    transition: all 0.3s $cubic;
    width: 165px;
    height: 107px;

    @media (max-width: 1024px) {
      width: calc((100% - 20px)/2);
    }

    span {
      font-size: 14px;
      font-weight: 700;
      line-height: 1.14;
      letter-spacing: 1.17px;
      margin-top: 18px;
    }

    & + * {
      margin-left: 25px;

      @media (max-width: 599px) {
        margin-left: 0;
      }
    }

    &--active {
      background: $color--olive;
      color: white;
    }
  }

  &-tab {
    position: relative;
    transition: all 0.3s $cubic;

    > div {
      max-height: 0;
      opacity: 0;
      overflow: hidden;
      transition: all 0.3s $cubic;
    }

    &--active {
      > div {
        max-height: 350px;
        opacity: 1;
      }
    }
  }
}

.video-play {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 58px;
  height: 58px;
}

.HowToUseSingle-wrapper{
  .video-wrapper{
    margin-bottom: 0;
  }
  @media screen and (max-width: 1024px){
    flex-flow: column-reverse;
  }
}

.HowToUseSingle-wrapper--left,
.HowToUseSingle-wrapper--right{
  width: 50%;
  @media screen and (max-width: 1024px){
    width: 100%;
  }
}
.HowToUseSingle-wrapper--left{
  margin-top:25px; 
  @media screen and (min-width: 1025px){
    padding-right: 60px;
    margin-top:0; 
  }
  @media screen and (min-width: 1200px){
    padding-right: 120px;
  }
  @media screen and (max-width: 768px){
    dd, ol, ul {
      padding-left: 16px;
    }
  }
  p{
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 26px;
    margin-bottom: 13px;
    strong{
      font-weight: 600;
      font-size: 18px;
      letter-spacing: 0.45px;
      line-height: 30px;
    }
    &:last-of-type{
      margin-bottom: 0;
    }
  }
}
.HowToUseSingle-wrapper-icons{
  margin-top: 14px;
    @media screen and (min-width: 1025px){
      display: flex;
      margin-top: 0;
    }
}
.HowToUseSingle-wrapper-icons--icon_wrapper--icon_item{
  border-bottom: 1px solid #3D3D38;
  margin-bottom: 10px;
  position: relative;
  @media screen and (max-width: 1024px){
    margin-right: 11px;
    border-bottom: none;
    margin-bottom: 0;
  }
  @media screen and (min-width: 1025px){
  min-height: 56px;
    > div{
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
    }
  }
  img{
    vertical-align: unset;
  }
  span.desktop-only{
    margin-left: 5px;
  }
}
.HowToUseSingle-wrapper-icons--icon_wrapper{
  display: flex;
  margin-top: 14px;
    @media screen and (min-width: 1025px){
      margin-top: 0;
      margin-left: 55px;
      display: block;
    }
    @media screen and (min-width: 1200px){
      min-width: 120px;
      margin-left: 70px;
    }
  &:first-child{
    margin-left: 0;
    margin-top: 0;
  }
}
.HowToUseSingle-wrapper-icons--icon_wrapper--icon_item{
  font-size: 16px;
  letter-spacing: 0.4px;
  line-height: 16px;
  font-weight: 600;
}
.HowToUseSingle-wrapper-icons--text_item{
  font-size: 16px;
  letter-spacing: 0.4px;
  line-height: 26px;
    @media screen and (max-width:1024px){
     font-weight: 600;
     font-size: 16px;
     letter-spacing: 0.4px;
     line-height: 16px;
     padding-top: 5px;
    }
}
.HowToUseSingle-featured_product{
  background: #F7F6F3;
  padding:20px 14px;
  display: flex;
  max-width: 360px;
}
.HowToUseSingle-featured_product--info{
  padding-left: 13px;
}
.HowToUseSingle-featured_product--image{
  max-width: 140px;
  @media screen and (max-width: 400px){
    max-width: 30%;
  }
}
p.HowToUseSingle-featured_product--heading{
  margin-top: 24px;
  margin-bottom: 9px !important;
  font-weight: 600;
  font-size: 16px;
  line-height: 17px;
  letter-spacing: normal;
  color:#292D43;
}
.HowToUseSingle-featured_product--product_title{
  margin: 0;
  font-size: 23px;
  line-height: 34px;
  letter-spacing: normal;
}
.HowToUseSingle-featured_product--product_subtitle{
  font-size: 14px;
  line-height: 24px;
  letter-spacing: 0.35px;
  margin-top: -3px;
}
.HowToUseSingle-featured_product--price{
  font-size: 14px;
  line-height: 22px;
  letter-spacing: 0.35px;
  font-weight: 600;
}
.HowToUseSingle-featured_product--button{
  margin-top: 5px;
  background:#AF5D00;
  padding: 14px 33px;
  font-family: "Avenir LT Pro", sans-serif;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 1.4px;
  color:#fff;
  text-transform: uppercase;
  cursor: pointer;
    @media screen and (max-width: 480px){
        padding: 14px 20px;
    }
  &:hover{
    background-color: #9a5a12;
  }
}

.HowToUseSingle-featured_product--info--stars{
    justify-content: left !important;
      position: relative;
    top: 4px;
    .yotpo--gra-star-icon:before{
      font-size: 12px;
    }
}

