.c-pdpCertified {
  background: $color--gray;
  padding: 46px 0;

  &__heading {
    font-size: 40px;
    line-height: 1.35;
    letter-spacing: 0;
    margin-bottom: 5px;
  }

  &__copy {
    font-weight: 400;
    letter-spacing: 0.4px;
    width: 90%;

    @media (max-width: 1024px) {
      width: 100%;  
    }
  }

  &__images {
    img {
      max-width: initial;
      max-height: 90px;

      @media (max-width: 1024px) {
        max-height: 60px;  
      }
    }
  }
}
