.accordion-faqs {
  align-items: center;
  display: flex;
  padding-bottom: 50px;

  &__list {
    flex: 0 0 50.8%;
    padding-right: 85px;

    @media (max-width: 1024px) {
      flex: 0 0 100%;
      padding-right: 0;
    }

    &-item {
      &-title {
        font-size: 18px;
        font-weight: 700;
        letter-spacing: 0.45px;
        line-height: 1.67;
        margin-bottom: 10px;
      }

      &-copy {
        font-weight: 400;
        letter-spacing: 0.4px;
        line-height: 1.625;
        a{
          text-decoration: underline;
        }
      }

      & + * {
        margin-top: 35px;
      }
    }
  }

  &__url {
    color: #B9680C;
    display: inline-block;
    font-weight: 500;
    letter-spacing: 0;
    line-height: 1.375;
    margin-top: 35px;
    text-decoration: underline;
  }

  &__image {
    flex: 1;
    min-width: 0;
  }
}