.radio-hidden {
  display: none !important;
}
.iwt-tabs {
  padding-top: 75px;
  padding-bottom: 75px;
}
.iwt-tabs-container {
  max-width: 1240px;
  margin: 0 auto;
}
.iwt-tabs-control {
  display: flex;
}
.iwt-tabs-menu {
  display: flex;
  align-items: center;
  text-align: right;
}
.tab-item {
  max-height: 0;
  overflow: hidden;
  display: flex;
}
.radio-hidden:checked + .tab-item {
  max-height: 500px;
}
.tab-item > * {
  width: 50%;
}
.tab-item .tab-content {
  display: flex;
  align-items: center;
  padding: 0 20px;
  max-width: 445px;
  margin: 0 auto;
}
.tab-content p {
  font-size: 18px;
  line-height: 31px;
  letter-spacing: 0.45px;
}
.tab-image img {
  max-height: 100%;
}
.iwt-tabs-menu-wrap .tab-heading {
  font-size: 14px;
  letter-spacing: 1.62px;
  cursor: pointer;
  margin: 15px 5px;
  display: inline-block;
  border-bottom: 2px solid #000;
  opacity: .4;
}
.iwt-tabs-menu-wrap .tab-heading.active {
  opacity: 1;
}
.tab-content-link {
  color: #af5e1b;
  font-weight: bold;
  margin-top: 20px;
  display: inline-block;
  border-bottom: 1px solid #af5e1b;
}
@media only screen and (max-width: 1280px) {
  .iwt-tabs-container {
    padding-left: 20px;
    padding-right: 20px;
  }
}
@media only screen and (max-width: 1024px) {
  .iwt-tabs-control {
    flex-wrap: wrap;
  }
  .iwt-tabs-control > * {
    width: 100%;
  }
  .iwt-tabs-control .iwt-tabs-menu {
    order: -1;
    padding: 25px 0;
  }
  .iwt-tabs-menu .iwt-tabs-menu-wrap {
    margin: 0 auto;
    display: flex;
  }
}
@media only screen and (max-width: 768px) {
  .iwt-tabs {
    padding-top: 20px;
    padding-bottom: 20px;
  }
  .tab-content {
    text-align: center;
  }
  .tab-content-link {
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 1.17px;
    margin-bottom: 20px;
  }
  .tab-item {
    flex-wrap: wrap;
  }
  .tab-item > * {
    width: 100%;
  }
  .tab-image {
    order: 1;
    padding: 20px 0;
  }
  .radio-hidden:checked + .tab-item {
    max-height: 2000px;
  }
}