.hero-home_wrapper{
    position: absolute;
    top: 50%;
    padding: 46px 38px;
    max-width: 616px;
    transform: translateY(-50%);
  }
  .hero-home-ambassador{
    margin-top: 122px;
  }
  .hero-home__new-content{
    position: relative;
    z-index: 2;
  }
  .hero-home_wrapper-background{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 1;
  }
  .ap_hero-home__sub-title{
    font-family: "Avenir LT Pro";
  font-size: 16px;
  letter-spacing: 0.8px;
  line-height: 26px;
  margin: 0;
  margin-bottom: 5px;
  }
  .ap_hero-home__title{
    font-family: "Adobe Garamond Pro";
    font-size: 54px;
    letter-spacing: 0;
    line-height: 60px;
    margin-bottom: 24px;
  }
  .ap_hero-home__copy{
    margin: 0;
    margin-bottom: 24px;
    font-family: "Avenir LT Pro";
  font-size: 16px;
  letter-spacing: 0.4px;
  line-height: 26px;
  }
  .hero-home__copy-second{
    font-family: "Avenir LT Pro";
  font-size: 16px;
  letter-spacing: 0.4px;
  line-height: 26px;
  }
  .hero-home__cta-main{
    color: white;
    background-color: #AF5D00;
  }
  .hero-home__cta-main:hover{
    background-color: white;
    color: #AF5D00;
  }
  .ap_hero-home__sub-title{
    font-weight: 600;
    text-transform: uppercase;
  }
  .ap_hero-home__copy{
    font-weight: 400;
  }
  .hero-home__copy-second{
    font-weight: 600;
  }
  .hero-home__cta-main{
    padding: 25px 0;
    max-width: 396px;
    max-height: 70px;
    text-align: center;
    margin-top:16px;
  }
  .hero-home__cta-main p{
    font-size: 16px;
    letter-spacing: 2px;
    line-height: 16px;
    text-align: center;
    cursor: pointer;
    color: white;
    font-weight: 600;
  }
  .hero-home__cta-main:hover p{
    color: #AF5D00;
  }
  .hero-home__cta-main a{
    font-size: 16px;
    letter-spacing: 2px;
    line-height: 16px;
    text-align: center;
    color: white;
    display: none;
    font-weight: 600;
  }
  .hero-home__cta-main:hover a{
    color: #AF5D00;
  }
  @media (max-width:1024px){
    .hero-home_wrapper{
      transform: none !important;
      position: relative;
      left: 0 !important;
      padding: 40px 27px 53px 27px;
    }
    .ap_hero-home__title{
      font-size: 42px;
      letter-spacing: 0;
      line-height: 54px;
      margin-bottom: 15px;
    }
    .ap_hero-home__copy{
      font-size: 14px;
      letter-spacing: 0.35px;
      line-height: 24px;
      margin-bottom: 15px;
    }
    .hero-home__copy-second{
      font-size: 16px;
  letter-spacing: 0.4px;
  line-height: 26px;
    }
    .hero-home__cta-main{
      margin-top: 10px;
    }
    .hero-home_wrapper-background{
      opacity: 1 !important;
    }
  }