.pdpImageHighlight{
    text-align:center;
    max-width:1125px;
    margin:0 auto 100px;
  }
  .pdpImageHighlight-wrapper{
    display:flex;
    flex-wrap: nowrap;
  }
  .pdpImageHighlight-block {
    width: 33.33%;
  }
  .pdpImageHighlight-top{
    margin-bottom:64px;
  }
  .pdpImageHighlight-heading{
    font-size:44px;
    line-height:54px;
    margin-bottom: 6px;
  }
  .pdpImageHighlight-description{
    font-size:18px;
    line-height:31px;
    max-width: 700px;
    margin: 0 auto;
  }
  .pdpImageHighlight-block-inner{
    background:#F0EEE9;
    margin:0 12px;
  }
  .pdpImageHighlight-block-inner_content{
    padding:30px;
    min-height: 166px;
  }
  .pdpImageHighlight-heading-small{
    font-size: 20px;
    line-height: 24px;
    margin-bottom: 10px;
  }
  .pdpImageHighlight-description-small{
    font-size: 14px;
    line-height: 24px;
    font-weight: 400;
  }
  @media (max-width: 768px){
    .pdpImageHighlight-top{
      margin-bottom:35px;
      padding:0 20px;
    }
    .pdpImageHighlight-heading{
      font-size:36px;
      line-height:48px;
    }
    .pdpImageHighlight-description{
      font-size:16px;
      line-height:24px;
    }
    .pdpImageHighlight{
      margin-bottom:70px;
    }
    .pdpImageHighlight-wrapper{
      display:block;
      max-width:351px;
      margin:0 auto;
    }
    .pdpImageHighlight-block {
      width: 100%;
      margin-bottom:45px;
    }
    .pdpImageHighlight-block:last-child {
      margin-bottom:0;
    }
  }
   @media (max-width: 380px){
     .pdpImageHighlight-wrapper{
      padding:0 10px;
     }
   }