.review-slider {
  background: $color--gray;
  padding-top: 110px;
  padding-bottom: 110px;

  @media (max-width: 1024px) {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  @media screen and (min-width: 768px) and (max-width: 1024px) {
    padding-bottom: 130px;
  }

  &__container {
    @include make-container;
    position: relative;
  }

  &--pagination{
    margin-top: 50px;
    @media screen and (min-width: 830px){
      bottom: -50px;
    }
    @media screen and (min-width: 768px){
      margin-top: 0;
      position: absolute;
      bottom: -100px;
      left: 51.5%;
      z-index: 100;
    }
  }

  &__slider {
    overflow: hidden;
    position: relative;
  }

  &__track {
    align-items: flex-start;
    box-sizing: content-box;
    display: flex;
    transition-property: transform;
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
  }

  &__item {
    align-items: center;
    display: flex;
    flex-shrink: 0;
    flex-wrap: wrap;
    max-width: 100%;

    @media (max-width: 767px) {
      align-items: flex-start;
    }
  }

  &__image {
    flex: 0 0 44.8%;

    @media (max-width: 767px) {
      flex: 0 0 100%;
      margin-bottom: 20px;
    }
  }

  &__text {
    flex: 1;
    min-width: 0;
    padding-left: 6.5%;

    @media (max-width: 767px) {
      flex: 0 0 100%;
      padding-left: 0;
    }
  }

  &__copy {
    font-family: 'Adobe Garamond Pro',serif;
    font-size: 32px;
    font-weight: 400;
    line-height: 1.31;
    letter-spacing: 0;
    margin-bottom: 10px;

    @media (max-width: 1024px) {
      font-size: 22px;  
    }

    @media (max-width: 767px) {
      font-size: 19px;  
    }
    @media (max-width: 480px) {
      font-size: 16px;  
    }
  }

  &__name {
    color: #222;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.71;
    letter-spacing: 0.35px;
  }

  &__pagination {
    display: flex;
    margin-top: 25px;
    margin-bottom: 15px;
  }

  &__dot {
    border: 4px solid transparent;
    border-radius: 50%;
    cursor: pointer;
    flex-shrink: 0;
    transition: all 0.3s $cubic;
    width: 50px;
    height: 50px;
    @media screen and (min-width:1301px){
      width: 68px;
      height: 68px;
    }

    img {
      border-radius: 50%;
      object-fit: cover;
      pointer-events: none;
      width: 100%;
      height: 100%;
    }

    & + * {
      margin-left: 25px;
      @media (max-width: 1024px) {
        margin-left: 16px;
      }
      @media (max-width: 768px) {
        margin-left: 6px;
      }
    }

    &--active {
      border-color: $color--brown;
    }
  }

  &__link {
    color: $color--brown;
    display: inline-block;
    font-weight: 700;
    line-height: 1.18;
    letter-spacing: 0;
    text-decoration: underline;

    &.mobile-only{
        display: none;
        @media screen and (max-width: 767px){
          display: block;
        }
      }
    &.desktop-only{
      display: block !important;
        @media screen and (max-width: 767px){
         display: none  !important;
       }
    }
  }
}

.rating {
  background: url('stars.svg') left/127px 19px no-repeat;
  margin-bottom: 30px;
  width: 127px;
  height: 19px;

  @media (max-width: 1024px) {
    margin-bottom: 15px;  
  }

  &-4 {
    width: calc(127px * 0.8);
  }

  &-3 {
    width: calc(127px * 0.6);
  }
}