.ResultsPage_i-wrapper{
    max-width: 1440px;
    margin:100px auto  100px auto;
}
.ResultsPage_i-heading{
    color: #3D3D38;
  font-family: "Adobe Garamond Pro";
  font-size: 40px;
  letter-spacing: 0;
  line-height: 54px;
  margin-bottom: 24px;
  margin-left: auto;
    margin-right: auto;
    max-width: 1201px;
}
.ResultsPage_i-options-wrap{
    border: 0;
    align-items: center;
    margin-bottom: 36px;
    padding: 0;
    margin-left: auto;
    margin-right: auto;
    max-width: 1201px;
}
.ResultsPage_i-option{
    display: flex;
    border-radius: 34px;
    justify-content: space-between;
    align-items: center;
    padding: 8px 49px 8px 7px;
}
.ResultsPage_i-option .ResultsPage_is-included-option-heading{
margin-top: 0;
}
.ResultsPage_i-wrapper .ResultsPage_is-included-option-image-wrap{
    margin-right: 16px;
    max-width: 54px;
    border: 1px solid #d1cfcc;
    border-radius: 50%;
}
.ResultsPage_i-option-link{
    font-family: "Avenir LT Pro";
  font-size: 13px;
  letter-spacing: 0;
  line-height: 22px;
  flex-basis: 30%;
  color: #B9680C;
  text-decoration: underline;
  text-align: right;
  cursor: pointer;
  font-weight: 600;
}
.ResultsPage_i-slider, .ResultsPage_i-slider1, .ResultsPage_i-slider2{
    max-width: 1326px;
    margin-right: 0;
}
.ResultsPage_i-slide-image{
    position: relative;
    margin-bottom: 12px;
    border-radius: 2px;
}
.ResultsPage_i-slide-image img{
    width: 100%;
}
.ResultsPage_i-slide-info{
    position: absolute;
    top: 10px;
    width: 22px;
    transition: all .3s;
    right: 10px;
    background-color: #b6ab91;
    border-radius: 50%;
    height: 22px;
    text-align: center;
    font-family: serif;
    color: white;
}
.ResultsPage_i-slide-info-overlay{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s;
    width: 100%;
    opacity: 0;
    height: 100%;
    background-color: rgba(61,61,56,0.75);
}
.ResultsPage_i-slide-info-overlay p{
    color: #FFFFFF;
    font-family: "Avenir LT Pro";
    font-size: 16px;
    letter-spacing: 0.4px;
    font-weight: 400;
    line-height: 26px;
    max-width: 232px;
    text-align: center;
}
.ResultsPage_i-slide-image:hover .ResultsPage_i-slide-info{
    opacity: 0;
}
.ResultsPage_i-slide-image:hover .ResultsPage_i-slide-info-overlay{
    opacity: 1;
}
.ResultsPage_i-slide-heading{
    color: #3D3D38;
    font-family: "Adobe Garamond Pro";
    font-size: 23px;
    letter-spacing: 0;
    margin-bottom: 4px;
    line-height: 34px;
}
.ResultsPage_i-slide-subheading{
    color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 14px;
  letter-spacing: 0.35px;
  font-weight: 600;
  line-height: 24px;
  text-transform: capitalize;
}
.ResultsPage-acc-href-mob{
    color: #b9680c;
    font-family: "Avenir LT Pro";
    font-size: 13px;
    letter-spacing: 0;
    line-height: 22px;
    margin-top: 24px;
    cursor: pointer;
    font-weight: 600;
    text-decoration: underline;
}
.ResultsPage_arrow-controls{
    display: flex;
    justify-content: flex-end;
    max-width: 1201px;
    margin-top: 24px;
   
}
.swiper-button-next, .swiper-button-prev{
    display: none;
}
.ResultsPage_arrow-controls-left{
    margin-right: 11px;
}
.ResultsPage_i-slider-handle{
    display: none;
}
.ResultsPage_i-slider-handle-active{
    display: block;
}
.ResultsPage_is-included-option-active .ResultsPage_is-included-option-image-wrap{
    border: 0;
}
.is--open .ResultsPage_is-included-option-image-wrap{
    border: none;
}
.ResultsPage_i-ingredients-modal{
    position: fixed;
    left: 50%;
    top:50%;
    transform: translate(-50%,-50%);
    border-radius: 3px;
    background-color: #F7F6F3;
    padding: 49px 50px;
    max-width: 80%;
    width: 1100px;
    transition: all 0.3s;
    z-index: 10;
    opacity: 0;
    pointer-events: none;
}
.ResultsPage_i-ingredients-modal-heading{
    color: #3D3D38;
    font-family: "Avenir LT Pro";
    font-size: 16px;
    letter-spacing: 0.8px;
    line-height: 26px;
    padding-bottom:12px;
    font-weight: 600;
    border-bottom:1px solid #3D3D38;
}
.ResultsPage_i-ingredients-modal-text{
    color: #222222;
    font-family: "Avenir LT Pro";
    font-size: 14px;
    letter-spacing: 0.35px;
    line-height: 24px;
    margin-top:14px;
}
.ResultsPage_i-ingredients-modal-close{
    margin-left: auto;
    cursor: pointer;
    transform: rotate(45deg);
    font-size: 26px;
    position: absolute;
    font-weight: 300;
    display: inline-block;
    right: 22px;
    top: 22px;
}
.ResultsPage_i-ingredients-modal-open{
    opacity: 1;
    pointer-events: all;
}
@media (max-width:1024px){
    .ResultsPage_is-included-options-mob .ResultsPage_is-included-option-results{
        padding: 20px 15px 40px 15px;
    }
    .ResultsPage_i-ingredients-modal-text{
        font-size: 13px;
        max-height: 200px;
        overflow: scroll;
        line-height: 17px;
    }
    .ResultsPage_i-ingredients-modal-heading{
        padding-bottom: 4px;
        font-size: 14px;
    }
    .ResultsPage_i-ingredients-modal{
        padding: 22px 15px;
    }
   
.ResultsPage_is-included-options-mob .FaqAccordion{
    margin-top: 45px;
    margin-bottom: 45px;
}
    .ResultsPage_is-acc-text{
      flex: 1;
    }

    .ResultsPage_arrow-controls{
        justify-content: flex-start;
        margin-bottom: 10px;
    }
    .ResultsPage_is-included-options{
        display: none;
    }
    .ResultsPage_i-slide-info-overlay{
        opacity: 1;
        display: block;
        background-color: transparent;
        position: static;
    }
    .ResultsPage_i-slide-info-overlay p{
        text-align: left;
        color: #3D3D38;
    }
    .ResultsPage-slider-mob{
        position: relative;
        max-width: 233px;
        overflow: visible;
    }
    .ResultsPage_i-wrapper .ResultsPage-slider-mob img{
        max-width: 223px;
        margin-bottom: 12px;
    }
    .ResultsPage_i-slider{
        display: none;
    }
    .ResultsPage_i-wrapper .ResultsPage-slider-mob .swiper-slide{
       max-width: 233px;
    }
    .ResultsPage_i-heading{
        display: none;
    }
}