     .QuickBuy-display-time{ 
       position: relative;
        padding-right: 47px;
        padding-left: 18px;
        border-left: 1px solid #F0EEE9;
        height: 42px;
        display: flex;
        align-items: center;
        color: #3D3D38;
        font-family: "Avenir LT Pro";
        font-size: 14px;
        letter-spacing: 0.7px;
        line-height: 14px;
        text-align: center;
     }
     .QuickBuy-display-time::after{
      position: absolute;
      content: "";
      background-image: url("https://cdn.shopify.com/s/files/1/0079/7193/0170/files/arrow.svg?v=1644409280");
      width: 15px;
      height: 8px;
      transform: rotate(180deg);
     }
  
     .QuickBuy__cf-wrapper{
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      transition: all 0.5s ease;
      background-color: white;
      max-height: 0%;
     }
     .QuickBuy_swiper-slide-wrapper:hover .QuickBuy__cf-wrapper{
      max-height: 100%;
    }
     .QuickBuy__cf-time-selector{
       display: none;
       text-align: center;
       border: 1px solid #3D3D38;
       padding: 8px 0;
       margin: 17px 15px 0px 15px;
     }
     .QuickBuy__cf-time-selected{
      color: #3D3D38;
      font-family: "Avenir LT Pro";
      font-weight: 800;
      font-size: 14px;
      letter-spacing: 0.7px;
    
      line-height: 16px;
      
      cursor: pointer;
     }
     .QuickBuy__cf-times{
      max-height: 0;
      transition: all 0.3s ease;
      overflow: hidden;
     }
     .QuickBuy__cf-times.open{
       padding-top: 3px;
       max-height: 84px;
     }
     .QuickBuy__cf-time{
      font-family: "Avenir LT Pro";
      font-size: 14px;
      padding-bottom: 12px;
      letter-spacing: 0.7px;
      cursor: pointer;
      font-weight: 600;
      line-height: 16px;
      text-align: center;
     }
     .QuickBuy__cf-time:last-of-type{
       padding-bottom: 10px;
     }
     .QuickBuy__cf-time-selected svg {
       transform: rotate(180deg);
       transition: all 0.3s;
     }
     .QuickBuy__cf-time-selected.visited svg {
      transform: rotate(0deg);
    }
     .QuickBuy__cf-time-selected.visited{
       padding-top: 8px;
       border-top: 1px solid #3D3D38;
     }
     .QuickBuy__cf-time:hover{
      color: #80894F;
      text-decoration: underline;
     }
     .QuickBuy__cf-select-circle{
      width: 18px;
      height: 18px;
      border-radius: 50%;
      border: 1px solid black;
      display: flex;
      justify-content: center;
      align-items: center;
      position: relative;
      box-shadow: inset 0 0 0 0px red, inset 0 0 0 2px #ffffff;
      margin-right: 7px;
     }
     .QuickBuy__cf-membership-select{
       margin-top: 11px;
     }
     .QuickBuy__cf-membership-select, .QuickBuy__cf-single-select{
       display: flex;
       padding: 0 15px;
       align-items: center;
       margin-bottom: 12px;
       cursor: pointer;
     }
     .QuickBuy__cf-membership-select.selected .QuickBuy__cf-select-circle, .QuickBuy__cf-single-select.selected .QuickBuy__cf-select-circle{
    
      background-color:  #3D3D38;;
      
     
     }
     
