     }
     .QuickBuy_swiper .swiper-wrapper{
       align-items: flex-start;
     }
     @media (max-width:800px){
      .QuickBuy-reviews{
        top: 5px;
        left: 5px;
      }
      .QuickBuy_heading{
        margin-bottom: 20px;
        font-size: 32px;
        line-height: 44px;
      }
      .QuickBuy__cf-wrapper{
        position: static;
        display: flex;
        flex-direction: column;
      }
      .QuickBuy__cf-membership-select, .QuickBuy__cf-single-select{
        margin-bottom: 10px;
        padding: 0;
      }
      .QuickBuy__cf-atc {
        margin-top: 13px;
        margin-bottom: 6px;
      }
      .QuickBuy__cf-time-selector{
        margin: 0;
      }
      .QuickBuy__cf-time-selected.visited{
        border-top: 0;
        padding-top: 0;
        padding-bottom: 8px;
        border-bottom: 1px solid #3d3d38;
      }
      .QuickBuy{
        background-color: white !important;
      }
      .QuickBuy__cf-time-selected svg{
        transform: rotate(0deg);
      }
      .QuickBuy__cf-time-selected.visited svg{
        transform: rotate(180deg);
      }
      .QuickBuy__cf-time{
        padding-bottom: 0;
        padding-top: 8px;
      }
      .QuickBuy__cf-time:last-of-type {
        padding-bottom: 0px;
    }
      .QuickBuy__cf-membership-select, .QuickBuy__cf-single-select{
        order: 2;
      }
      .QuickBuy__cf-atc{
        order: 1;
      }
      .QuickBuy__cf-time-selector{
        order: 4;
        flex-direction: column-reverse;
      }

      .swiper-pagination.swiper-pagination-QuickBuy.swiper-pagination-clickable.swiper-pagination-bullets.swiper-pagination-horizontal{
        display: block;
      }
       .QuickBuy_swiper-title{
        font-size: 28px;
        letter-spacing: 0;
        line-height: 38px;
        margin-top: 7px;
       }
       .QuickBuy_swiper-subtitle{
        font-size: 14px;
