/* We are just overriding theme.layout.css and assistance.css */

html{
  scroll-behavior: smooth;
}
body{
  overflow-y:hidden; 
  &.template__product, 
  &.template__product--pdp-combo{
    line-height: 0;
    .main-content{
      line-height: 26px;
    }
    .bis-reset .bis-button{
      line-height: 26px;
    }
  }
}

/* fix for side cart */
.c-drawer__body{
  line-height: 26px;
}

/* main layout */
.pdp-regular-template{
  padding:96px 0 0 0;
  &.promo-bar--removed{
    padding:59px 0 0 0;
  }
@media screen and (min-width: 48rem){
    padding:115px 0 0 0;
      &.promo-bar--removed{
        padding:80px 0 0 0;
      }
  }
  @media screen and (min-width: 64rem){
    padding:106px 0 0 0;
   &.promo-bar--removed{
     padding:71px 0 0 0;
  }
   .c-productImage__col {
      width: 52%;
      margin: 0;
    }
    .c-productInfo__col{
      width: 48%;
    }
    .c-product__row{
      align-items: flex-start;
    }
    .c-productInfo__col--wrapper{
      max-width: 690px;
      margin:0 auto;
      padding: 30px 85px 0;
    }
  }
  .c-productInfo__col{
    padding-left: 20px;
    padding-right: 20px;
    @media screen and (min-width: 64rem){
      padding: 0;
    }
  }
}


/* gallery changes */
.c-productImage__featured-regularTemplate{
  margin-bottom: 0 !important;
}
.c-productImage__gallery{
  position: relative;
}
.pdp-regular-template{
  .slick-arrow{
    display: none !important;
  }
}
.c-productImage__thumbs-regularTemplate{
    margin: 0;
    padding-left: 20px;
    padding-right: 20px;
  }
.c-productImage__thumbs .slick-current img{
  border-width: 2px;
}
.c-productImage__featured-regularTemplate{
  margin-bottom: 25px;
}
@media screen and (max-width: 47rem){
  .c-productImage__thumbs .c-productImage__item {
    padding: 0 3px;
    width: 63px !important;
  }
  .c-productImage__thumbs-regularTemplate{
    margin: 0 auto;
    padding-left: 0;
    padding-right: 5px;
    img{
      border-width:0;
    }
    .slick-current img{
      outline: 2px solid #af5d00;
      outline-offset: -2px;
    }
  }
}

@media screen and (min-width: 67rem){
  .c-productImage__thumbs-regularTemplate{
    padding: 0;
    position: absolute !important;
    left: 25px;
    top: 25px;
    width: 82px;
    /* transform: translate(0, -50%); */
    margin-bottom: 35px;
    .slick-current img{
      border-color: #af5d00;
    }
    .slick-slide{
      float: none;
    }
    .c-productImage__item{
      width: 82px !important;
      padding: 0;
    }
  }
}

/* Top text */
.c-productInfo__header{
  margin-bottom: 22px;
}
.c-productInfo__title{
  font-size: 32px;
  line-height: 44px;
  letter-spacing: normal;
  @media screen and (min-width: 64rem){
    font-size: 40px;
    line-height: 54px;
  }
}
.c-productInfo__currentVariant{
  position:unset;
  padding-top: 0;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 1.7px;
  font-weight: 400;
   .c-productInfo__productType{
    padding-right: 0;
    color:#222
  }
}
.c-productInfo__description{
  text-align: center;
  p{
    font-size: 18px;
    line-height: 31px;
    letter-spacing: .45px;
    font-weight: 500;
  }
}

/* Reviews */
.c-productInfo__review{
 .yotpo{
    div.yotpo-bottomline{
      .yotpo-icon-half-star, .yotpo-icon-empty-star,.yotpo-icon-star{
        font-size: 21px;
      }
     .text-m{
          font-size: 14px;
          font-weight: 500;
          line-height: 22px;
          letter-spacing: .2px;
          pointer-events:none;
        }
    }

    .standalone-bottomline .star-clickable{
      align-items: flex-end;
    }
  }
}

/* Metafield icons */
.c-productInfo__icons{
  margin-bottom: 20px;
  max-width: unset;
  @media screen and (max-width: 63rem){
    flex-flow:wrap;
  }
}
.c-productInfo__icon{
  padding: 0 8px;
  justify-content: flex-start;
  @media screen and (max-width: 63rem){
      margin-bottom: 14px;
      padding: 0;
      text-transform: uppercase;
      &.c-productInfo__icon--regular{
        width: 50%;
      }
  }
  svg{
    max-height: 24px;
  }
}
.c-productInfo__icon-image{
  display: block;
  max-height: 27px;
  margin-bottom: 10px;
}
.c-productInfo__icon--img{
  width: auto !important;
  height: 100%;
}
.c-productInfo__icon-title{
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 1.17px;
  font-weight: 700;
  color:#222;
}

/* btns, form etc*/
.c-productInfo__submit{
  .c-quantityAdjust{
    max-width: 78px;
      @media screen and (max-width: 63rem){
        background:rgba(182,171,145,0.2);
        border:none;
        width:100px;
        min-width:100px;
        max-width:100px;
      }
  }
  .btn{
    width: 100% !important;
    margin-left: 13px;
    height: 60px;
    @media screen and (max-width: 63rem){
      height: 54px;
    }
    &.btn--oos{
      margin-left: 0;
    }
  }
}
/* Upscribe selection input fields with prices */
.cu-productTypeSelect__itemWrap.is-active .cu-productTypeSelect__itemLabel:before,
.cu-option.is-active:before{
  display: none;
}

.cu-productTypeSelect__itemContent-check{
    background-color: #b6ab9166;
    border: 0;
    border-radius: 50%;
    margin: .3rem 12px .3rem 0;
    width: 20px;
    height: 20px;
      svg{
        display: none;
      }
}

.c-productInfo__subscription .cu-upscribe{
  .cu-productTypeSelect__itemContent{
    position: unset;
    transform: unset;
    align-items: flex-end;
    justify-content: unset;
    padding: 0;
  }
}
.cu-productTypeSelect-selectorInner{
  display: flex;
  flex: 1;
  justify-content: space-between;
}
.c-productInfo__btnText,
.btn--oos{
  width: 100%;
  font-family: Avenir LT Pro;
  height: unset;
  line-height: 16px;
  letter-spacing: 2px;
  font-weight: 700;
  position: relative;
  top: 2px;
  i{
    font-style: normal;
  }
}
.cu__total-price{
  position: relative;
  padding-left: 18px;
  margin-left: 3px;
  &:before{
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 3px;
    width: 7px;
    height: 7px;
    border-radius: 50%;
    background-color: #fff;
  }
}
.cu-option.is-active .cu-option__mainText:before{
  display: none;
}
.cu-upscribe{
  .cu-productTypeSelect__itemSub{
    margin-top: 10px;
  }
  .cu-productTypeSelect__itemText,
    .cu-productTypeSelect__itemText-price{
      font-size: 14px;
      letter-spacing: .7px;
    }
  .cu-productTypeSelect__itemSub.is-active,
  .cu-productTypeSelect__itemWrap.is-active{
    .cu-productTypeSelect__itemBox:before{
      display: none;
    }
    .cu-productTypeSelect__itemLabel .cu-productTypeSelect__itemBox{
      background-color: #80894F;  
    }
    .cu-productTypeSelect__itemText,
    .cu-productTypeSelect__itemText-price, strike{
      color:#fff;
    }
    .cu-productTypeSelect__itemContent-check{
      background-color: #fff;
     svg{
        display: block;
        width: 12px;
        margin: 4px;
          g#Group-20 {
            fill: #80894F;
          }
      }
    }
  }
  .cu-productTypeSelect{
    display: unset;
  }
  .cu-productTypeSelect__itemWrap{
    width: 100%;
    height: auto;
  }
  .cu-productTypeSelect__itemLabel{
    background: none;
    border:none;
    padding: 0;
  }
  .cu-productTypeSelect__itemBox{
    min-height: unset !important;
    background-color: rgba(182, 171, 145, 0.2);
    padding: 10px 21px 9px 12px;
    border-radius: 2px;
  }
}


/* Upscribe Frequency input fields */
.rc_label_frequency__header{
  padding-top: 0;
  .cu-optionsTitle{
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 1.7px;
    font-weight: 700;
    color:#222;
  }
}
.cu-optionsGrid{
  margin-left: -7px;
  margin-right: -7px;
  .cu-option{
  border:0;
  background-color: transparent;
  padding: 0 7px;
    .cu-option__mainText{
        background-color: rgba(182, 171, 145, 0.2);
        min-height: 48px;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.7px;
        border-radius: 2px;
        &:before{
          display: none;
        }
        .days-60:before{
          content:"Popular";
          font-size: 12px;
          letter-spacing: .7px;
          line-height: 14px;
          width: 100%;
          @media screen and (max-width: 63rem){
            content:"Most Popular";
            position: absolute;
            bottom:-34px;
            color: #3D3D38;
            opacity: .65;
            left: 50%;
            transform: translate(-50%, 0);
          }
        }
    }

    &.is-active .cu-option__mainText{
      background-color: #80894F;
      span, .days-60:before{
        color: #fff;
        opacity: 1;
      }
      .days-60:before{
        @media screen and (max-width: 63rem){
          color: #3D3D38 !important;
          opacity: .65;
        }
      }
    }
  }
}
.c-productInfo__submit{
  margin: 35px 0 15px;
   @media screen and (min-width: 64rem){
      margin: 20px 0 15px;
   }
   .quantityAdjust{
    font-size: 16px;
    line-height: 16px;
    letter-spacing: 0.8px;
    font-weight: 700;
    border-color: #D1CFCC;
   }
}

.cu-productTypeSelect__itemText2-price{
  font-size: 14px;
  letter-spacing: .7px;
}

/* Sticky CTA */
.c-productInfo__col.sticky{
  .oos-form--posting{
    top: 17px;
  }
  .cu-description{
    display: none;
  }
  .c-productInfo__review{
   .yotpo{
      div.yotpo-bottomline{
        .yotpo-icon-half-star, .yotpo-icon-empty-star,.yotpo-icon-star{
          font-size: 17px;
        }
      }
    }
  }
  .cu-productTypeSelect__itemBox{
    .cu-productTypeSelect__itemText, 
    .cu-productTypeSelect__itemText-price{
      font-size: 16px !important;
      line-height: 26px !important;
      letter-spacing: .8px !important;
    }
    .cu-productTypeSelect__itemText-price{
      margin-left: 8px;
    }
    .cu-productTypeSelect-selectorInner{
      justify-content: center;
    }
    #getDiscountPrice:before{
      margin-right: 5px;
    }
  }
  .cu-optionsGrid .cu-option__mainText span{
    font-size: 16px !important;
    line-height: 26px !important;
    letter-spacing: .8px !important;
  }
  .c-productInfo__header {
    margin-bottom: 15px;
  }
  .c-productInfo__title{
    line-height: 30px;
    margin-top: 10px;
  }
  .c-productInfo__header, .c-productInfo__description,
  .cu-productTypeSelect__itemText-price strike,
  .cu-productTypeSelect__itemText2-price,
  .cu-productTypeSelect__itemText2-price strike,
  .cu-productTypeSelect__itemText--save{
    display: none;
  }
  .cu-productTypeSelect__itemText-price:before{
    content: "  ";
  }
  .c-productInfo__submit .btn{
    transition: unset !important;
  }
}
@media screen and (max-width: 1380px){
  .c-productInfo__col.sticky{
    .c-productInfo__header{
      display: none !important;
    }
  }
}
@media screen and (max-width: 63rem){
  .c-productInfo__col.sticky{
      #getOriginalPrice:before, 
      #getDiscountPrice:before{
        display: none;
      }
    .c-productInfo__submit .btn{
      width: 60% !important;
      margin-left: 0;
      max-height: 50px;
      &.btn--oos{
        width: 55% !important;
      }
    }
  }
}
@media (min-width:992px){
  .c-productInfo__col.sticky{
    padding-top: 10px !important;
    padding-bottom: 10px !important;
    .cu-description,.cu-productTypeSelect__itemContent-check{
      display: none;
    }
    .c-productInfo__submit .btn{
      width: 100% !important;
    }
    #add-item-form{
      align-items: center;
      min-width: 500px;
    }
    .c-productInfo__submit{
      width: auto !important;
    }
    .c-productInfo__submit .btn, .quantityAdjust{
      height: 60px;
      min-width: auto !important;
      width: 235px !important;
      margin: 0;
      max-height: 60px;
    }
    .quantityAdjust{
      margin-left: 4px;
      max-width: 105px;
    }
     .cu-productTypeSelect__itemWrap{
      margin-top: 0;
     }
    .c-productInfo__col--wrapper{
      max-width: 100%;
      margin: 0;
      width: 100%;
      display: flex;
      padding: 0;
    }
    .c-productInfo__header{
      display: block;
    }
    .cu-productTypeSelect__itemBox{
      height: 60px !important;
      .cu-productTypeSelect__itemText,
      .cu-productTypeSelect__itemText-price{
        color: #21211e;
      }
    }
    .cu-productTypeSelect__itemSub .cu-productTypeSelect__itemContent{
      width: 100% !important;
    }
     .cu-productTypeSelect{
      &.open{
        .cu-productTypeSelect__itemWrap.is-active{
         .cu-productTypeSelect__itemLabel{
            background-color: #80894f !important;
              color:#fff;
           }
           .cu-productTypeSelect__itemText-price{
              color:#fff;
           }
        }
      }
    }
    .cu-option__mainText{
      max-height: 60px !important;
      height: 60px !important;
    }
    .cu-optionsGrid{
      height: 60px !important;
      width: 150px !important;
      left: -237px !important;
      .cu-option.is-active .cu-option__mainText{
        span, span:before{
          color: #21211e;
        }
        span:before{
          opacity: 0.5;
        }
      }
      &.open{
        max-height: 174px !important;
        height: 174px !important;
       .cu-option__option, .cu-option__option.is-active{
         max-height: 58px !important;
         height: 58px !important;
         border-radius: 2px;
        }
        &.is-active{
          background-color: #80894f !important;
          color:#fff;
        }
      }
       .cu-option__option{
        // transition:unset !important;
        border-radius: 2px;
        padding: 0 !important;
        }
    }
    .cu-productTypeSelect:after,
    .cu-productTypeSelect.open:after,
    .cu-optionsGrid:after{
      bottom: 21px !important;
      filter: brightness(0);
      top: auto !important;
      right: 23px !important;
    }
    .cu-productTypeSelect.open:after,
    .cu-optionsGrid.open:after{
      filter: brightness(1);
    }
    .cu-upscribe{
      height: 60px !important;
    }
  }
}

@media screen and (min-width: 1381px){
  .c-productInfo__col.sticky{
    padding-top: 0px !important;
    padding-bottom: 0px !important;
  }
}

/* Botom text/links */
.c-productFrequency__text{
   font-size: 14px;
  line-height: 22px;
  letter-spacing: normal;
  font-weight: 600;
}
.cu-description{
  font-size: 14px;
  line-height: 24px;
  letter-spacing: .35px;
  margin-top: 15px;
  font-weight: 500;
}
.cu-description--hide{
  display: none;
}
.c-productInfo__footerLinks--regular-template{
  justify-content:unset;
  margin: 0;
  .c-productFrequency{
    margin: 0;
  }
}

.c-productFrequency__text{
  top: 0;
   .c-productFrequency__icon{
    text-decoration: none;
    position: relative;
    top: 3px;
    margin-right: 3px;
    svg{
      display: inline-block;
    }
  }
}
.c-productFrequency__last{
  margin-left: 15px;
}



/* Out of stock */
.oos-wrapper{
  background-color: #F7F6F3;
  padding: 25px 18px 16px;
  margin-bottom: 10px;
}
.oos-title{
  font-family: "Avenir LT Pro", "Helvetica Neue","Arial",sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 1.17px;
  font-weight: 800;
  text-transform: uppercase;
  color:#C8480F;
  margin-bottom: 19px;
}
.oos-description{
  font-size: 14px;
  line-height: 24px;
  letter-spacing: .35px;
  font-weight: 500;
}
.oos-form-input{
    font-family: "Avenir LT Pro", "Helvetica Neue","Arial",sans-serif;
    padding: 17px 16px;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: .35px;
    border: 1px solid #D1CFCC;
    border-radius: 1px;
    width: 100%;
    margin-top: 12px;
}
#oos-form{
  position: relative;
  &.active-loader{
     .oos-form-input,.oos-form-submit{
      opacity: 0.3;
     }
  }
}
.oos-form--posting{
  display: none;
  position: absolute;
  left: 17px;
  top: 26px;
  color:#c8480f;
  font-weight: 600;
}
.active-loader{
  .oos-form--posting{
    display: block;
    animation: blinker 1s linear infinite;
  }
}
@keyframes blinker {
  50% {
    opacity: 0;
  }
}
.oos-form-submit{
  margin: 9px 0 0;
}
.btn.oos-btn-klaviyo{
  margin-left: 0;
  @media (max-width: 47rem){
    font-size: 13px;
  }
}
.oos-info{
  margin: 15px 0 0;
  font-weight: 500;
}
.oos-info--success{
  color: #657543;
  margin: 0;
}
.oos-info--error{
  color:#c8480f;
}
.oos-form-input--error{
  border-color:#c8480f;
}

/* sticky OOS*/
.c-productInfo__col.sticky{
  .oos-sticky--wrapper{
    justify-content: space-between;
    .customize-btn, .submit-message{
      display: none;
    }
    .c-productInfo__submit .btn{
      width: 100% !important;
    }
  }
  .cu-productTypeSelect__itemBox{
    padding: 19px 0;
  }
  .cu-option__mainText span{
    top: 55%;
    transform: translateY(-55%);
  }
  .cu-description{
    margin-top: 6px;
  }
  .cu-optionsGrid.open:after, 
  .cu-productTypeSelect.open:after{
    top:6px;
    bottom: auto;
    right: 9px;
  }
  .oos-wrapper{
    padding: 0;
    margin: 0;
    position: relative;
    @media screen and (max-width: 991px){
      padding-top: 10px;
    }
  }
  .oos-description, .oos-title, .customize-btn,
  .cu__total-price{
    display: none;
  }
  #oos-form{
    display: flex;
  }
  .oos-form-input{
    margin:0 10px 0 0;
    min-width: 300px;
    @media (max-width: 63rem){
      min-width: auto;
      max-height: 50px;
    }
  }
  .oos-info{
    display: none;
    position: absolute;
    left: 10px;
    top: -16px;
    @media (max-width: 63rem){
    top: -14px;
    font-size: 12px;
    }
  }
  .oos-info--success{
    position: unset;
    text-align: center;
    font-size: 18px;
    @media (max-width: 63rem){
      font-size: 14px;
    }
  }
  .oos-btn-klaviyo{
    padding: 0 50px;
    @media (max-width: 47rem){
      padding: 0 25px;
      .c-productInfo__btnText{
        font-size: 13px;
        i{
          display: block;
          font-size: 10px;
        }
      }
    }
    @media (max-width: 37rem){
      padding: 0;
      .c-productInfo__btnText{
        font-size: 12px;
      }
    }
  }
}


/* Combo version */
@media (max-width: 37rem){
  .combobuy__icons{
    width: 100vw;
    position: relative;
    margin-left: -50vw;
    left: 50%;
    padding: 0 20px;
  }
}
.combobuy_icons-image{
  max-height: 50px;
  height: 50px;
}
.comboBuy__products{
  display: none;
}
.c-productInfo__icon--oos{
  .c-productInfo__icon-image, .c-productInfo__icon-title{
    opacity: 0.4;
  }
}
.c-productInfo__icon-title--combo{
  text-transform: uppercase;
  width: 100%;
}
.c-productInfo__info{
  text-transform: uppercase;
  color: #C8480F;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 1.17px;
  font-weight: 700;
  margin-top: 10px;
}
.c-productInfo__info--active{
  display: none;
}
.comboBuy__prod-description,
.comboBuy__prod-description p{
  font-size: 16px;
  line-height: 26px;
  letter-spacing: 0.4px;
  margin: 0;
}


/* upscribe style transferred from pdp-info snippet */
.cu-upscribe {
    position: relative;
    width: 100%;
    margin-top: 20px;
    padding: 0 1px;
}

.cu-productTypeSelect{
    flex-wrap: wrap;
    display: flex;
    flex-direction: row;
}

.cu-productTypeSelect__itemWrap {
    width:50%;
    display: flex;
}

.cu-productTypeSelect__itemLabel {
    position: relative;
    margin: 0;
    font-size: 16px;
    letter-spacing: .23px;
    line-height: 21px;
    color: #21211e;
    font-weight: 400;
    text-transform: capitalize;
    border: 1px solid #D1CFCC;
    padding: 4px;
    background: #fff;
    width: 100%;
    line-height: normal;
    cursor: pointer;
    display: block;
}

 .cu-productTypeSelect__itemLabel input{
    opacity: 0;
    visibility: hidden;
    height:0 !important;
    width:0 !important;
    overflow: hidden;
 }

.cu-productTypeSelect__itemWrap.is-active .cu-productTypeSelect__itemLabel{
    border-color: #AF5D00;
    border-width: 1px;
}

.cu-productTypeSelect__itemWrap.cu-productTypeSelect__itemSub.is-active .cu-productTypeSelect__itemLabel{
    border-right-width: 1px;
}

.cu-productTypeSelect__itemWrap.cu-productTypeSelect__itemOneTime.is-active .cu-productTypeSelect__itemLabel{
    border-left-width: 1px;
}

.cu-productTypeSelect__itemWrap.is-active .cu-productTypeSelect__itemLabel{
    border-color: #AF5D00;
    border-width: 1px;
}

.cu-productTypeSelect__itemWrap.is-active .cu-productTypeSelect__itemLabel .cu-productTypeSelect__itemBox{
    background: #f7f6f3;
}

.cu-productTypeSelect__itemBox{
    background: #fff;
    min-height: 80px;
    text-align: center;
    position: relative;
}

@media (min-width: 768px) {
    .cu-productTypeSelect__itemBox {
        min-height: 101px;
    }
}

.cu-option__mainText:before,
.cu-productTypeSelect__itemBox:before{
    content: "";
    width:16px;
    height:16px;
    left:5px;
    top:7px;
    background:url("https://cdn.shopify.com/s/files/1/0079/7193/0170/t/472/assets/icon-check-new.svg") no-repeat 0 0;
    background-size:16px auto;
    z-index: 10;
    position:absolute;
    opacity: 0;
}

.cu-productTypeSelect__itemWrap.is-active .cu-productTypeSelect__itemBox:before{
    opacity: 1;
}


.cu-productTypeSelect__itemContent {
    font-size: 16px;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    padding: 0;
    font-weight: 700;
}

.cu-productTypeSelect__itemText{
    position: relative;
}

.cu-options {
    width: 100%;
    margin-bottom: 5px;
}

.cu-options--frequency {
    margin-top: 14px;
}

@media (min-width: 768px) {
    .cu-options--frequency {
        margin-top: 23px;
    }
}

.cu-optionsTitle {
    display: block;
    margin-bottom: 0px;
    font-size: 13px;
    letter-spacing: 2px;
    font-weight: bold;
    text-transform: uppercase;
    text-align: left;
}

.cu-optionsGrid {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
}
.cu-optionsGrid:after, .cu-productTypeSelect:after{
    filter: brightness(4);
}
.cu-option {
    font-size: 16px;
    letter-spacing: .23px;
    line-height: 21px;
    color: #21211e;
    font-weight: 400;
    text-transform: capitalize;
    border: 1px solid #D1CFCC;
    padding: 4px;
    background: #fff;
    width: 33.3333%;
    position: relative;
    cursor: pointer;
}

.cu-option:active,
.cu-option:hover,
.cu-option:focus,
.cu-option.is-active {
    border-color: #AF5D00;
    background: none;

}

.cu-option:nth-child(1).is-active{
    border-left-width:1px;
    border-top-width:1px;
    border-bottom-width:1px;
}
.cu-option:nth-child(2).is-active{
    border-top-width:1px;
    border-bottom-width:1px;
}
.cu-option:nth-child(3).is-active{
    border-right-width:1px;
    border-top-width:1px;
    border-bottom-width:1px;
}

.cu-option.is-active .cu-option__check {
    display: block;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.cu-option.is-active .cu-option__check svg {
    width: 13px;
    fill-color: white;
    fill: white;
    margin-left: -1px;
    margin-bottom: -1px;
}

.cu-option__check {
    display: none;
    position: absolute;
    transform: translateX(-50%) translateY(-50%);
    left: 50%;
    top: -2px;
    background-color: #7FC464;
    border-radius: 100px;
    color: #fff;
    width: 24px;
    height: 24px;
}

.cu-option__mainText {
    background: #fff;
    min-height: 60px;
    text-align: center;
    position: relative;
    display: block;
}

@media (min-width: 768px) {
    .cu-option__mainText {
        min-height: 70px;
    }
}

.cu-option.is-active .cu-option__mainText {
    background: #f7f6f3;
    position:relative;
}

.cu-option.is-active .cu-option__mainText:before {
    opacity:1;
}

.cu-option__mainText span{
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 100%;
    padding: 0 20px;
    font-weight: 700;
}

.cu-option__subText {
    font-size: 16px;
    width: 100%;
    max-width: 250px;
    margin-ttop: 6px;
    letter-spacing: 0;
    font-weight: 400;
    text-align: center;
}


.cu-popup {
    display: inline-block;
    padding: 8px;
}
.cu-popup__hover {
    display: inline-block;
    position: relative;
    height: 35px;
    clear: both;
    cursor: pointer;
}
.cu-popup__block {
    display: none;
    position: absolute;
    text-align: left;
    left: -200px;
    right: auto;
    bottom: 160%;
    font-size: 13px;
    top: inherit;
    background-color: #222;
    width: 300px;
    border-radius: 5px;
    z-index: 50;
}
@media (min-width: 500px) {
    .cu-popup__block {
        left: -3px;
        right: auto;
    }
}
.cu-popup__block--isOpen {
    display: block;
}
.cu-popup__block:before {
    display: none;
    content: "";
    width: 1px;
    border: 10px solid transparent;
    border-bottom-color: black;
    position: absolute;
    top: -20px;
    left: 0;
    border-bottom-color: #3D3D38;
    border-bottom-color: transparent;
    border-top-color: #3D3D38;
    top: inherit;
    left: 3px;
    bottom: -20px;
}
@media (min-width: 500px) {
    .cu-popup__block:before {
        display: block;
    }
}
.cu-popup__block__content {
    line-height: 1.5;
    padding: 16px;
    font-size: 13px;
    color: #fff;
    margin-bottom: 8px;
    letter-spacing: 0;
}
.cu-popup__block__content a {
    color: #32a6fb;
    margin-top: 5px;
    display: inline-block;
}
.cu-popup__block__footer {
    padding: 0;
    margin-bottom: 25px;
    text-align: right;
    height: 28px;
}
.cu-popup__icon {
    vertical-align: middle;
    position: relative;
    height: 40px;
    top: 2px;
}
.cu-popup__popup {
    display: none;
    position: absolute;
    top: 100%;
    cursor: default;
    left: 0;
}
.cu-infoOpener {
    display: inline-block;
    position: relative;
    transform: translateY(3px);
    cursor: pointer;
}

.cu-infoOpener svg {
    width: 20px;
    height: 20px;
}

.cu-infoOpener:hover .cu-popup__block,
.cu-infoOpener:active .cu-popup__block,
.cu-infoOpener:focus .cu-popup__block {
    display: block;
}

