.ResultsPage-rs-wrapper{
    background-color: #F7F6F3;
}
.ResultsPage-rs-subwrapper{
    max-width: 1201px;
    padding: 200px 0;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.ResultsPage-rs-subwrapper-heading{
    color: #AF5D00;
font-family: "Adobe Garamond Pro";
font-size: 48px;
letter-spacing: 0;
line-height: 58px;
margin-bottom: 10px;
}
.ResultsPage-rs-subwrapper-text{
    max-width: 555px;
}
.ResultsPage-rs-subwrapper-text img{
    height: 18px;
    width: 144px;
    margin-bottom: 10px;
}
.ResultsPage-rs-subwrapper-subheading{
    opacity: 0.6;
  color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 14px;
  letter-spacing: 0.35px;
  line-height: 24px;
  margin-bottom: 33px;
}
.ResultsPage-rs-subwrapper-quote{
    color: #3D3D38;
  font-family: "Adobe Garamond Pro";
  font-size: 32px;
  letter-spacing: 0;
  line-height: 42px;
  margin-bottom: 16px;
}
.ResultsPage-rs-subwrapper-quote-author{
    color: #222222;
    font-family: "Avenir LT Pro";
    font-size: 14px;
    letter-spacing: 0.35px;
    line-height: 24px;
}
.ResultsPage-rs-subwrapper-quote-author{
    color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 14px;
  letter-spacing: 0.35px;
  line-height: 24px;
  font-weight: 600;
}
.ResultsPage-rs-wrapper .fs-entry-container:last-of-type{
    transform: translateY(77px);
    margin-left: 66px;
}
.ResultsPage-rs-wrapper .fs-entry-container:first-of-type{
    transform: translateY(-77px);
}
.ResultsPage-rs-video{
    position: relative;
    overflow: hidden;
    width: 100%;
    padding-top: 56.25%;
   
    min-width: 253px;
    min-height: 453px;
}
.ResultsPage-rs-video:first-of-type{margin-right: 70px;transform: translateY(-70px);}
.ResultsPage-rs-video:last-of-type{margin-right: 70px;transform: translateY(70px);}
.ResultsPage-rs-videos{
    display: flex;
}
.ResultsPage-rs-videos-mob .swiper-slide{
    max-width: 243px;
}
.ResultsPage_check-title-before-price{
    text-decoration: line-through;
}
.ResultsPage-rs-video iframe {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    height: 100%;
    outline: transparent;
}
.ResultsPage_arrow-controls-rs{
    display: none;
}
.ResultsPage-rs-videos-mob{display: none;}
@media (max-width:1024px){
    .ResultsPage-rs-subwrapper-heading{
        font-size: 32px;
        letter-spacing: 0;
        line-height: 44px;
        margin-bottom: 15px;
    }
    .ResultsPage-rs-videos{
        margin: auto;
    }
    .ResultsPage-rs-video:first-of-type{margin-right: 70px;transform: translateY(-13px);}
    .ResultsPage-rs-subwrapper-subheading{
        font-size: 14px;
  letter-spacing: 0.35px;
  line-height: 24px;
  margin-bottom: 20px;
    }
    .ResultsPage-rs-subwrapper-quote{
        font-size: 28px;
  letter-spacing: 0;
  line-height: 38px;
  margin-bottom: 15px;
    }
    .ResultsPage-rs-subwrapper-quote-author{
        font-size: 14px;
  letter-spacing: 0.35px;
  line-height: 24px;
  margin-bottom: 32px;
    }
    .ResultsPage-rs-subwrapper-text{
        padding: 0 28px;
    }
    .ResultsPage-rs-subwrapper{
        flex-wrap: wrap-reverse;
    }
  
.ResultsPage-rs-subwrapper-text{
    margin: auto;
}
.ResultsPage-rs-subwrapper{
    padding: 65px 0;
}
}
@media (max-width:650px){
    .ResultsPage_arrow-controls-rs{
        display: block;
        padding: 0 28px;
        margin: auto;
        max-width: 555px;
    }
    .ResultsPage-rs-wrapper{
        padding-bottom: 50px;
    }
    .ResultsPage-rs-videos-scrollbar{
        bottom: -14px !important;
    }
    .ResultsPage-rs-videos{display: none;}
    .ResultsPage-rs-videos-mob{
        display: block;
        max-width: 253px;
        overflow: visible;
        margin-left: 28px;
    }
    .ResultsPage-rs-videos-scrollbar{
        width: 80vw !important;
    }
    .ResultsPage-rs-subwrapper{
        padding-bottom: 0px;
    }
    .ResultsPage-rs-video{
        min-height: 340px;
    }
}