.ingredients {

  @media (min-width: 1025px) {
    position: relative;
  }

  &-header {
    align-items: center;
    display: flex;
    justify-content: space-between;
    margin-bottom: 35px;
  }

  &-tabs {
    align-items: center;
    display: flex;

    &__item {
      background: $color--gray;
      border-radius: 8px;
      color: $color--dark;
      cursor: pointer;
      font-size: 14px;
      font-weight: 700;
      line-height: 1.14;
      letter-spacing: 1.17px;
      padding: 5px 25px 5px 10px;
      transition: all 0.4s $cubic;
      text-transform: uppercase;
      white-space: nowrap;
      width: 225px;

      img {
        margin-right: 5px;
        width: 50px;
        height: 50px;
      }

      &--active {
        background: $color--olive;
        color: white;
      }

      & + * {
        margin-left: 20px;
      }
    }
  }

  &-all {
    position: relative;
    height: 22px;

    &__item {
      color: $color--brown;
      cursor: pointer;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.375;
      letter-spacing: 0;
      transition: all 0.3s $cubic;
      opacity: 0;
      position: absolute;
      top: 0;
      right: 0;
      text-decoration: underline;
      visibility: hidden;
      white-space: nowrap;

      &--active {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &-slider {
    overflow: hidden;
    position: relative;
    transition: all 0.4s $cubic;
    max-height: 0;

    @media (max-width: 1024px) {
      max-height: none;
      &:not(.ingredients-slider-regular-pdp){
        padding: 10px 0 35px 20px;
      }
    }

    &--active {
      max-height: 500px;
    }

    &__wrapper {
      padding-bottom: 50px;
      position: relative;

      .swiper-control {
        display: flex;
        justify-content: flex-end;

        img {
          cursor: pointer;

          & + img {
            margin-left: 10px;
          }

          &.swiper-button-disabled {
            opacity: 0.6;
          }
        }
      }
    }

    &__track {
      align-items: flex-start;
      box-sizing: content-box;
      display: flex;
      transition-property: transform;
      position: relative;
      width: 100%;
      min-height: 398px;
      z-index: 1;
    }

    &__item {
      flex-shrink: 0;
      width: 268px;

      @media (max-width: 1024px) {
        width: 233px;  
      }

      &-image {
        border-radius: 2px;
        margin-bottom: 10px;
        overflow: hidden;
        position: relative;
        height: 268px;
        width: 100%;

        @media (max-width: 1024px) {
          height: 233px;
        }
        @media (min-width: 1025px) {
          &::before {
            background: rgba(black, 0.3);
            content: "";
            display: block;
            opacity: 0;
            position: absolute;
            top: 0;
            left: 0;
            transition: all 0.4s $cubic;
            transition-delay: 0.1s;
            transform: scale(0);
            transform-origin: 100% 0;
            width: 100%;
            height: 100%;
          }
        }
        &::after {
          align-items: center;
          background: $color--black;
          border-radius: 50%;
          color: white;
          content: "i";
          font-family: serif;
          display: flex;
          justify-content: center;
          position: absolute;
          top: 10px;
          right: 10px;
          width: 22px;
          height: 22px;

          @media (max-width: 1024px) {
            display: none;  
          }
        }

        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }

        p {
          align-items: center;
          box-sizing: border-box;
          color: white;
          display: flex;
          font-size: 16px;
          font-weight: 400;
          letter-spacing: 0.4px;
          line-height: 1.625;
          justify-content: center;
          opacity: 0;
          padding: 0 30px;
          position: absolute;
          top: 0;
          left: 0;
          text-align: center;
          transition: all 0.3s $cubic;
          transform: translateY(20px);
          width: 100%;
          height: 100%;
        }

        &:hover {
          &::after {
            display: none;
          }
          &::before {
            opacity: 1;
            transition-delay: 0s;
            transform: scale(1);
          }

          p {
            opacity: 1;
            transition-delay: 0.25s;
            transform: translateY(0);
          }
        }

        @media (max-width: 1024px) {
          .description{
            display: none;
          }
        }
      }

      &-title {
        font-family: "Adobe Garamond Pro";
        font-size: 23px;
        font-weight: 400;
        line-height: 1.47;
        margin-bottom: 8px;

        @media (max-width: 1024px) {
          margin-bottom: 4px;
        }
      }

      &-subtitle {
        color: $color--dark;
        font-size: 14px;
        font-weight: 700;
        letter-spacing: 1.17px;
        line-height: 16px;
        text-transform: uppercase;

        @media (max-width: 1024px) {
          margin-bottom: 10px;  
        }
      }

      &-description {
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
        letter-spacing: 0.35px;
      }

    }
  }

  
  &-mob {
    padding-bottom: 35px;

    .show-all-ingredients {
      color: $color--brown;
      cursor: pointer;
      font-size: 16px;
      font-weight: 500;
      line-height: 1.375;
      letter-spacing: 0;
      margin-top: 20px;
      text-decoration: underline;
    }
    .swiper-control {
      display: none;
     }
  }
  &-all--bottom{
    left: 0;
    bottom: 85px;
    z-index:0;
    max-width:500px; 
     .ingredients-all__item{
        position: unset;
     }
  }

  @media (max-width: 1024px) {
    &-all--bottom{
      position: relative;
      left: unset;
      bottom: 38px;
    }
    .swiper-control {
      display: none;
     }
  }
}
.all-ingredients-data--body{
    max-width: 800px;
    margin: 0 auto;
  }
.all-ingredients-data,
.all-ingredients-data-m {
  background: $color--gray;
  font-weight: 400;
  opacity: 0;
  padding: 50px 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transition: all 0.3s $cubic;
  transform: translate(-50%, -50%);
  visibility: hidden;
  width: 60vw;
  max-width: 900px;
  height: auto;
  z-index: 2;
  @media (max-width: 1024px) {
    padding: 35px 20px;
    width: calc(100vw - 40px);  
      &.all-ingredients-data-m--active,
      &.all-ingredients-data--active{
      position: fixed;
      max-height: 500px;
      z-index: 10003;
        .all-ingredients-data--body{
          overflow-y: auto;
          position: relative;
          height: 445px;
          display: block;
        }
      }
  }

  &-close {
    cursor: pointer;
    font-size: 17px;
    line-height: 1;
    position: absolute;
    top: 16px;
    right: 15px;
    z-index: 1;
  }

  &--active {
    opacity: 1;
    visibility: visible;
  }
}