.ResultsPage_selector{
margin-top: 30px;
margin-bottom: 30px;
}
.ResultsPage_results-title-wrapper{
    display: flex;
    width: 170px;
    justify-content: space-between;
    align-items: center;
}
.ResultsPage_result1, .ResultsPage_result2{
    text-align: center;
    width: 70px;
}
.ResultsPage_result1-text, .ResultsPage_result2-text{
    font-weight: 600;
    color: #3D3D38;
    font-family: "Avenir LT Pro";
  font-size: 14px;
  letter-spacing: 0;
  line-height: 21px;
  padding-top: 8px;
  text-align: center;
}
.nav-mobile__logo .icon{
    fill: #3d3d38;
}
.nav-mobile__cart-trigger .icon{
    fill: #3d3d38;
}
.nav-mobile__trigger span:after{
    background-color: #3d3d38;
}
.nav-mobile__trigger span:before{
    background-color: #3d3d38;
}
.nav-mobile__trigger span{
    background-color: #3d3d38;
}
.ResultsPage_wrapper{
    background-color: #F7F6F3;
}
.ResultsPage{
    margin-top: 125px;
    max-width: 1440px;
   
    padding: 50px 67px 111px 106px;
}
.ResultsPage_column-heading{
    color: #3D3D38;
    font-family: "Avenir LT Pro";
    font-size: 16px;
    letter-spacing: 0.8px;
    margin: 0;
    text-transform: uppercase;
    font-weight: 600;
    line-height: 26px;
}
.ResultsPage_column-title{
    color: #3D3D38;
    font-family: "Adobe Garamond Pro";
    font-size: 54px;
    letter-spacing: 0;
    line-height: 60px;
    margin-bottom: 30px;
}
.ResultsPage_column-range{
    color: #80894F;
}
.ResultsPage_check{
    border: 0;
    margin: 0;
    position: absolute;
    top: 10px;
    left: 12px;
}
.ResultsPage_oos-message-heading{
    color: #AF5D00;
    font-family: "Avenir LT Pro";
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 26px;
    font-weight: 600;
    margin-bottom: 6px;
}
.ResultsPage_oos-message-subheading{
    color: #222222;
    font-weight: 600;
    font-family: "Avenir LT Pro";
    font-size: 14px;
    letter-spacing: 0.35px;
    line-height: 24px;
}
.ResultsPage_oos-message{
    margin-top: 20px;
}
.ResultsPage_check-wrapper{
    padding: 10px 20px 10px 0px;
}
.ResultsPage_check-title{
    color: #3D3D38;
  font-family: "Avenir LT Pro";
  font-size: 14px;
  letter-spacing: 0.7px;
  font-weight: heavy;
  line-height: 26px;
}
.ResultsPage_check-parent{
    flex: 1; padding: 0; border: 1px solid #d1cfcc;
}
.comboBuy__purchaseSelector--active {
    border: 1px solid #af5d00;
}
.ResultsPage_column{
    margin: 0 auto 0 0;
    width: 47%;
}
.ResultsPage_check-title-details{
    display: flex;
    flex: 1;
    margin-left: 44px;
    justify-content: space-between;
}
.ResultsPage_results-title{
    color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 12px;
  letter-spacing: 1px;
  line-height: 12px;
  font-weight: bold;
  margin-bottom: 30px;
  text-transform: uppercase;
  font-weight: 600;
}
.comboBuy__frequency-title{
    color: #222222;
    font-family: "Avenir LT Pro";
    font-size: 12px;
    letter-spacing: 1px;
    line-height: 14px;
}
.ResultsPage_frequency{
    margin-top: 30px;
}
.ResultsPage_result-text{
    color: #3D3D38;
    font-family: "Avenir LT Pro";
    font-size: 16px;
    letter-spacing: 0;
    line-height: 26px;
    font-weight: 400;
}
.ResultsPage_frequency-title{
    margin-bottom: 11px;
    color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 12px;
  letter-spacing: 1px;
  line-height: 14px;
}
.ResultsPage_frequency-wrapper{
    margin: 0;
}
.ResultsPage_frequency-item{
    display: flex;
    width: 30%;
    flex-direction: column;
    border: 0;
    justify-content: center;
    border-radius: 2px;
    background-color: rgba(182,171,145,0.2);
}
.ResultsPage_frequency-item:first-of-type{
    padding: 23px 12px;
}
.ResultsPage_frequency-item:last-of-type{
    padding: 23px 12px;
}
.ResultsPage_frequency-item:nth-of-type(2){
    padding: 16px 12px;
}

.ResultsPage_frequency-item span{
    color: #3D3D38;
    font-family: "Avenir LT Pro";
    font-size: 14px;
    letter-spacing: 0.7px;
    line-height: 17px;
    text-align: center;
  
}
.ResultsPage_button{
    justify-content: center;
    margin-bottom: 0;
    height: 70px;
}
.ResultsPage_button-wrapper{
    display: flex;
    justify-content: space-between;
   
}
span.ResultsPage_frequency-popular{
    opacity: 0.5;
    color: #3D3D38;
    font-family: "Avenir LT Pro";
    font-size: 10px;
    font-weight: 600;
    letter-spacing: 1px;
    line-height: 12px;
    text-align: center;
  }
  .ResultsPage_number-count{
      display: flex;
      max-width: 129px;
      justify-content: center;
      padding: 20px 24px;
      margin-right: auto;
      border: 1px solid #D1CFCC;
  border-radius: 2px;
  background-color: #FFFFFF;
  align-items: center;
  height: 70px;
  }
  .ResultsPage_button-subwrapper{
      margin: auto
  }
  .ResultsPage_number-count span{
    color: #3D3D38;
    font-family: "Avenir LT Pro";
    font-size: 16px;
    letter-spacing: 0.8px;
    font-weight: 600;
    line-height: 16px;
    text-align: center;
    font-weight: 800;
  }
  .ResultsPage_minus{
      padding-right: 15px;
      cursor: pointer;
  }
  .ResultsPage_plus{
    padding-left: 15px;
    cursor: pointer;
  }
  .ResultsPage_thumbnail{
      width: 8px;
      height: 8px;
      background-color: #3d3d38 ;
      border-radius: 50%;
  }
  .ResultsPage_thumbnail-wrapper{
      width: 40px;
      height: 8px;
  }
.ResultsPage-slider{
    overflow: hidden;
    position: relative;
}
  .ResultsPage-slider .swiper-pagination-bullet{
    background-color: rgba(61,61,56,0.2);
  }
  .ResultsPage-slider .swiper-pagination-bullet-active{
    background-color: #3D3D38;
  }
  .ResultsPage-slider .swiper-pagination{
      width: 11%;
      bottom: 20px;
      left: 20px;
  }
  .ResultsPage-swiper-overlay{
    position: absolute;
    top: 20px;
    border: 1px solid rgba(151, 151, 151,0.15);
    left: 20px;
    font-family: "Avenir LT Pro";
    background-color: rgba(182,171,145,.15);
    color: #3d3d38;
    height: 12px;
    padding: 15px 23px;
    padding-top: 16px;
    border-radius: 27px;
    text-transform: uppercase;
    display: flex;
    font-size: 12px;
    align-items: center;
    letter-spacing: 1px;
    z-index: 1;
    font-weight: 600;
  }
  .ResultsPage-swiper-overlay-dot{
      width: 9px;
      margin-bottom: 3px;
      margin-left: 8px;
      height: 9px;
      border-radius: 50%;
      background-color: #3D3D38;
  }
  .ResultsPage_image-slider-col{
    width: 688px;
  }
  .ResultsPage_retake-wrapper{
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-top: 15px;
  }
  .ResultsPage_retake-wrapper svg{
      width: 18px;
  }
  .ResultsPage_retake-wrapper a{
    color: #B9680C;
    font-family: "Avenir LT Pro";
    font-size: 13px;
    letter-spacing: 0;
    line-height: 24px;
    font-weight: 400;
    margin-left: 8px;
    text-decoration: underline;
  }
  .ResultsPage_retake-wrapper a:hover{
      text-decoration: none;
  }
  .ResultsPage_column{
    max-width: 515px;
    display: flex;
    flex-direction: column;
    margin-right: 64px;
    justify-content: center;
  }
  .ResultsPage_check g#Group-20{
      fill: rgba(182,171,145,0.4);
  }
  .ResultsPage_check{
      background-color: rgba(182,171,145,0.4);
      padding: 4px;
  }
   .comboBuy__purchaseSelector--active  .ResultsPage_check{
        background-color:white;
  }
   .comboBuy__purchaseSelector--active  .ResultsPage_check g#Group-20{
    fill: #80894F;
}
.ResultsPage_selector .comboBuy__purchaseType-selector{
    border: 0;
    border-radius: 2px;
    background-color: rgba(182,171,145,0.2);
    
}
.ResultsPage_selector .comboBuy__purchaseSelector--active{
    border: 0;
    background-color: #80894F !important;
    opacity: 1;
}
.ResultsPage_selector .comboBuy__purchaseSelector--active p{
   color: white !important;
}
.ResultsPage_selector .comboBuy__purchaseSelector--active .comboBuy__purchaseType-selectorInner{
    background-color: transparent;
}
.ResultsPage_check-parent:first-of-type .ResultsPage_check-wrapper{
    margin-bottom: 10px;
}
.ResultsPage_frequency-item.comboBuy__frequency-item--active{
    border: 0 !important;
    background-color: #80894F;
}
.ResultsPage_frequency-item.comboBuy__frequency-item--active span{
   color:white !important;
   opacity: 1;
}
.ResultsPage_frequency .comboBuy__frequency-selector{
    justify-content: space-between;
}
.ResultsPage-row-wrapper{
    flex-wrap: nowrap;
}
.ResultsPage_check-title-mob{
    display: none;
}
.ResultsPage_button-wrapper .comboBuy__addtocart--active:hover{
    background-color: #9a5a12;
}
.ResultsPage-discount-wrapper{
    display: none;
    position: fixed;
    max-width: 375px;
    background-color: white;
    z-index: 9;
    left: 25px;
    bottom: 25px;
    padding: 15px;
}
.ResultsPage-discount-wrapper-text{
    font-family: "Avenir LT Pro";
    font-size: 14px;
    color: #3d3d38;
    letter-spacing: .7px;
    font-weight: 400;
}
.ResultsPage-discount-wrapper-text-bold{
    font-family: "Avenir LT Pro";
    font-size: 14px;
    color: #3d3d38;
    letter-spacing: .7px;
    font-weight: 600;
    line-height: 14px;
}
@media(min-width:1024px){
   .ResultsPage_wrapper .c-product__row{
        align-items: flex-start;
    }
}
@media (max-width:1024px) {
    .ResultsPage_oos-message{
        margin: 10px 0;
    }
    .ResultsPage-row-wrapper{
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .ResultsPage_column{
        width: 100%;
        max-width: 100%;
    }
    .ResultsPage{
        padding: 41px 21px 0px 21px;
    }
    .ResultsPage_column{
        margin: auto;
        margin-bottom: 45px;
    }
    .ResultsPage_image-slider-col{
        width: 100%;
    }
    .ResultsPage_image-slider-col .c-productImage__gallery{
        max-width: 100%;
    }
    .ResultsPage_column-heading{
        font-size: 14px;
        letter-spacing: 0.7px;
        line-height: 26px;
    }
    .ResultsPage_column-title{
        font-size: 42px;
        letter-spacing: 0;
        line-height: 54px;
        margin-bottom: 15px;
    }
    .ResultsPage_results-title{
        font-size: 12px;
        letter-spacing: 1px;
        line-height: 12px;
        margin-bottom: 15px;
    }
    .ResultsPage_result-text{
        font-size: 16px;
        letter-spacing: 0;
        line-height: 26px;
        margin-bottom: 16px;
    }
    .ResultsPage_check-title-desk{
        display: none;
    }
    .ResultsPage_check-title-mob{
        display: block;
    }
    .ResultsPage_check-title{
        font-size: 14px;
        letter-spacing: 0.7px;
        line-height: 14px;
    }
    .ResultsPage_check-title-mob{
        font-size: 14px;
        letter-spacing: 0.7px;
        line-height: 14px;
    }
    .ResultsPage_check-title-mob span{
        font-size: 12px;
        letter-spacing: 0.7px;
        line-height: 10px;
        margin-top: 6px;
        display: block;
    }
    .ResultsPage_selector{
        margin: 0;
        margin-bottom: 24px;
    }
    .ResultsPage_check-wrapper{
        padding: 17px 21px 17px 0;
    }
    .ResultsPage_check-parent:first-of-type .ResultsPage_check{
        top: 13px;
    }
    .ResultsPage_check-parent:last-of-type .ResultsPage_check{
        top: 20px;
    }
    .ResultsPage_check-title-details{
        align-items: center;
    }
    .ResultsPage_column .comboBuy{
        margin-top: 0;
    }
    .ResultsPage_frequency-title{
        font-size: 12px;
        letter-spacing: 1px;
        line-height: 14px;
        margin-bottom: 11px;
    }
    .ResultsPage_frequency-item:first-of-type, .ResultsPage_frequency-item:last-of-type{
        padding: 18px 23px;
    }
    .ResultsPage_frequency-item:nth-of-type(2){
        padding: 10px 23px;
    }
    .ResultsPage-slider .swiper-pagination{
        width: 14%;
    }
    .ResultsPage_frequency-item span{
        font-size: 13px;
        letter-spacing: 0.7px;
        line-height: 17px
    }
    span.ResultsPage_frequency-popular{
        font-size: 12px;
        letter-spacing: 0.7px;
        line-height: 10px;  
    }
    .ResultsPage_frequency{
        margin: 0;
        margin-bottom: 24px;
    }
    .ResultsPage_number-count{
        max-width: 92px;
        height: 60px;
    }
    .ResultsPage_button .comboBuy__addtocart-text{
        width: auto;
        font-size: 16px;
        letter-spacing: 2px;
        line-height: 16px;
    }
    .ResultsPage_button{
        height: 60px;
    }
    .ResultsPage_minus{
        padding: 0 16px;
    }
    .ResultsPage_plus{
        padding: 0 15px;
    }
    .ResultsPage_image-slider-col .c-productImage__col{
        margin: 0;
    }
    .c-product__row.ResultsPage-row-wrapper{
        padding-bottom: 0;
    }
    .ResultsPage_image-slider-col{
        margin-bottom: 0;
    }
}
@media (max-width:400px) {
    .ResultsPage-discount-wrapper{
        bottom: 0;
        left: 0;
        width: 100%;
        max-width: 400px;
    }
}