.FeaturedReviews_heading{
  color: #3D3D38;
  font-family: "Adobe Garamond Pro";
  font-size: 40px;
  letter-spacing: 0;
  line-height: 54px;
  text-align: center;
  margin-bottom: 10px;
}
.FeaturedReviews_img{
  width: 144px;
  height: 18px;
  margin: auto;
}
.FeaturedReviews_swiper-slide-text-wrapper{
  background-color: rgba(0,0,0,0.55);
  position: absolute;
  opacity: 0;
  transition: opacity 0.3s;
  top: 0;
  height: 100%;
  left: 0;
  display: flex;
  justify-content: center;
    align-items: center;
  text-align: center;
}
.FeaturedReviews_swiper-slide-text{
  color: #FFFFFF;
  font-family: "Adobe Garamond Pro";
  font-size: 23px;
  letter-spacing: 0;
  font-weight: 400;
  line-height: 34px;
  padding: 0 23px;
}
.FeaturedReviews_swiper-slide{
  display: flex;
  align-items: flex-start;
}
.FeaturedReviews_subheading{
  opacity: 0.6;
  color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 14px;
  letter-spacing: 0.35px;
  line-height: 24px;
  margin-top: 10px;
  text-align: center;
  margin-bottom: 50px;
}
.FeaturedReviews{
  text-align: center;
  padding-top: 100px;
  padding-bottom: 157px;
}
.FeaturedReviews_swiper-slide{
  max-width: 365px;
}
.FeaturedReviews_swiper-slide:hover .FeaturedReviews_swiper-slide-text-wrapper{
  opacity: 1;
}
.FeaturedReviews_swiper-slide-heading{
  color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 14px;
  letter-spacing: 1.17px;
  line-height: 17px;
  margin-top:12px;
  text-align: left;
  text-transform: uppercase;
  font-weight: 600;
}
.FeaturedReviews_swiper-slide-wrapper-img{
  position: relative;
}
.FeaturedReviews_swiper-slide-info{
  position: absolute;
  top: 10px;
  right: 10px;
  height: 15px;
  width: 15px;
  display: flex;
  border-radius: 50%;
  padding: 2px;
  align-items: center;
  font-family: serif;
  background-color: #ffffffb3;
  justify-content: center;
}
.FeaturedReviews_swiper-slide:hover .FeaturedReviews_swiper-slide-info{
  display: none;
}
.FeaturedReviews_swiper-wrapper{
  display: flex;
  align-items: flex-start;
}
@media (max-width:600px){
  .FeaturedReviews_swiper-slide.swiper-slide-active .FeaturedReviews_swiper-slide-text-wrapper{
    position: static;
    opacity: 1;
    background-color: white;
  }
  .FeaturedReviews_swiper-slide{
    max-width: 307px;
  }
 
  .FeaturedReviews_swiper-slide-info{
    display: none;
  }
  .FeaturedReviews_swiper-slide-text{
    color: #3D3D38;
    font-family: "Adobe Garamond Pro";
    font-size: 21px;
    letter-spacing: 0;
    line-height: 29.4px;
    text-align: left;
    padding-top: 21px;
  }
  .FeaturedReviews_swiper-slide-heading{
    margin-top: 0;
    padding-top: 12px;
    padding-left: 21px;
    padding-bottom: 20px;
    background-color: white;
    color: #222222;
    font-family: "Avenir LT Pro";
    font-size: 14px;
    letter-spacing: 1.17px;
    line-height: 16px;
  }
  .FeaturedReviews{
    text-align: center;
    padding: 70px 0;
  }
  .FeaturedReviews_heading{
    font-size: 32px;
    line-height: 44px;
  }
  .FeaturedReviews{
    background-size: cover;
  }
}