#shopify-section-quizPage{
    margin-top: 202px;
}
.QuizPage_wrapper{
    max-width: 866px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 136px;
}
.QuizPage_step-title{
    font-size: 32px;
    margin-bottom: 16px;
  letter-spacing: 0;
  line-height: 42px;
  text-align: center;
}
.QuizPage_step-subtitle{
    font-size: 16px;
    letter-spacing: 0.4px;
    line-height: 26px;
    transition: all 0.3s;
    margin-bottom: 42px;
    text-align: center;
    font-family: Avenir LT Pro;
}
.QuizPage-option{
    display: flex;
    justify-content: space-between;
    max-width: 645px;
    margin: auto;
}
.QuizPage-option-subwrapper{
    border: 1px solid #D1CFCC;
    border-radius: 2px;
    width: 187px;
    height: 211px;
    transition: all 0.3s;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}
.QuizPage-option-title{
    margin-bottom: 0;
    margin-top: 10px;
    letter-spacing: 0.8px;
    font-family: Avenir LT Pro;
    font-size: 16px;
  line-height: 26px;
  font-weight: 600;
  text-align: center;
  color: #3d3d38;
}
.QuizPage_next-button{
    width: 225px;
    font-size: 13px;
  letter-spacing: 1.5px;
  line-height: 18px;
  text-transform: uppercase;
  font-weight: 600;
  text-align: center;
  pointer-events: none;
  opacity: 0.8;
  margin: 42px auto 0 auto;
  color: white;
  cursor: pointer;
  padding: 19px 0;
  background-color: #AF5D00;
}
.QuizPage_next-button-approved{
    opacity: 1;
    pointer-events: all;
}
.QuizPage_step2-options{
    display: flex;
    max-width: 645px;

    flex-wrap: wrap;
    margin: auto;
    justify-content: space-between;
}
.QuizPage_step2-option{
    width: 305px;
    border-radius: 2px;
  background-color: rgba(182, 171, 145,0.2);
  padding: 19px 0 15px 35px;
  margin-bottom: 20px;
  font-size: 14px;
  letter-spacing: 1px;
  color: #3D3D38;
  font-family: "Avenir LT Pro";
  line-height: 14px;
  transition: all 0.3s;
  cursor: pointer;
  font-weight: 600;
}
.QuizPage_buttons .QuizPage_next-button{
    margin: 0;
}
.QuizPage_loader{
    position: fixed;
    top: 50%;
    left: 50%;
    text-align: center;
    transform: translate(-50%, -50%);
    width: 100%;
    display: none;
    background-color: white;
    height: 100%;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.lds-default {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-default div {
    position: absolute;
    width: 6px;
    height: 6px;
    background: #3d3d38;
    border-radius: 50%;
    animation: lds-default 1.2s linear infinite;
  }
  .QuizPage_loader-text{
    font-size: 22px;
    letter-spacing: .7px;
    line-height: 26px;
    transition: all .3s;
    margin-bottom: 42px;
    text-align: center;
    font-family: "Adobe Garamond Pro", serif;
    color: #3d3d38;
    font-weight: 400;
  }
  
  .lds-default div:nth-child(1) {
    animation-delay: 0s;
    top: 37px;
    left: 66px;
  }
  .lds-default div:nth-child(2) {
    animation-delay: -0.1s;
    top: 22px;
    left: 62px;
  }
  .lds-default div:nth-child(3) {
    animation-delay: -0.2s;
    top: 11px;
    left: 52px;
  }
  .lds-default div:nth-child(4) {
    animation-delay: -0.3s;
    top: 7px;
    left: 37px;
  }
  .lds-default div:nth-child(5) {
    animation-delay: -0.4s;
    top: 11px;
    left: 22px;
  }
  .lds-default div:nth-child(6) {
    animation-delay: -0.5s;
    top: 22px;
    left: 11px;
  }
  .lds-default div:nth-child(7) {
    animation-delay: -0.6s;
    top: 37px;
    left: 7px;
  }
  .lds-default div:nth-child(8) {
    animation-delay: -0.7s;
    top: 52px;
    left: 11px;
  }
  .lds-default div:nth-child(9) {
    animation-delay: -0.8s;
    top: 62px;
    left: 22px;
  }
  .lds-default div:nth-child(10) {
    animation-delay: -0.9s;
    top: 66px;
    left: 37px;
  }
  .lds-default div:nth-child(11) {
    animation-delay: -1s;
    top: 62px;
    left: 52px;
  }
  .lds-default div:nth-child(12) {
    animation-delay: -1.1s;
    top: 52px;
    left: 62px;
  }
  
.QuizPage_buttons{
    display: flex;
    align-items: center;
    color: #B9680C;
    font-weight: 600;
  font-family: "Avenir LT Pro";
  margin-top: 42px;
  font-size: 14px;
  letter-spacing: 0;
  line-height: 16px;
  justify-content: center;
}
.QuizPage_buttons p{
    padding-top: 2.5px;
    padding-left: 8px;
}
.QuizPage_back-button{
    display: flex;
    margin-right: 33px;
cursor: pointer;
}
.QuizPage_back-button svg{
    padding-top: 2px;
}
.QuizPage_step3-options{
    display: flex;
    justify-content: space-between;
    max-width: 865px;
    margin: auto;
}
.QuizPage_step3-option{
    border: 1px solid #D1CFCC;
    border-radius: 2px;
    width: 185px;
    height: 209px;
    display: flex;
    flex-direction: column;
    transition: all 0.3s;
    cursor: pointer;
    justify-content: center;
    align-items: center;
}
.QuizPage_step4-options{
    display: flex;
    max-width: 645px;
    flex-wrap: wrap;
    margin: auto;
    justify-content: space-between;
} 
.QuizPage_step4-option{
