.c-pdpInstagram {
  padding-top: 75px;
  padding-bottom: 125px;

  h3 {
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.8px;
    margin-bottom: 36px;
  }

  .fs-wrapper .fs-entry-container div.fs-timeline-entry {
    margin: 0 25px;
  }

  .fs-wrapper div.fs-entry-container {
    padding: 0 !important;
    width: 270px !important;
    height: 220px !important;
  }
}

body .instagram .fs-slider-next-button, .instagram .fs-slider-prev-button {
  background: none;
  opacity: 1;
}
body .instagram .fs-slider-next-button, .instagram .fs-slider-prev-button{
  outline: none;
}
 
body .instagram .fs-arrow {
  stroke-width: 2px;
  stroke: $color--black;
}