.whats-included {
  position: relative;
    @media (min-width: 1024px) {
      margin-top: -85px;
    }
  &__container {
    @include make-container;
  }

  &__top {
    margin-bottom: 40px;
    text-align: center;

    @media (max-width: 1024px) {
      margin-bottom: 20px;  
    }
  }

  &__title {
    font-size: 40px;
    line-height: 1.35;
    letter-spacing: 0;
    margin-bottom: 5px;

    @media (max-width: 1024px) {
      font-size: 32px;  
    }
  }

  &__copy {
    font-weight: 400;
    line-height: 1.6;
    letter-spacing: 0.4px;
  }

  &__main {
    display: flex;
    flex-wrap: wrap;
  }

  &__sidebar {
    display: grid;
    flex: 0 0 25.8%;
    gap: 20px;

    @media (max-width: 1024px) {
      flex: 0 0 100%;
      order: 2;
    }

    &-item {
      background: $color--gray;
      padding: 20px 25px;
      text-align: center;

      .rating {
        background: url(stars.svg) left/98px 14px no-repeat;
        margin: 0 auto 8px;
        width: 98px;
        height: 14px;
      }

      &-title {
        font-family: 'Avenir LT Pro';
        font-size: 16px;
        line-height: 1.625;
        font-weight: 700;
        letter-spacing: 0.8px;
        margin-bottom: 5px;
        text-transform: uppercase;
      }

      &-copy {
        color: $color--dark;
        font-size: 14px;
        font-weight: 400;
        line-height: 1.71;
        letter-spacing: 0.35px;
        margin-bottom: 5px;
      }

      &-name {
        color: $color--dark;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.35px;
        font-weight: 700;
      }
    }
  }

  &__map {
    flex: 1;
    min-width: 0;
    padding-left: 35px;
    position: relative;

    @media (max-width: 1024px) {
      flex: 0 0 calc(100% + 40px);
      order: 1;
      margin-left: -20px;
      margin-right: -20px;
      margin-bottom: 35px;
      padding-left: 0;
    }

    img {
      object-fit: cover;
      width: 100%;
      height: 100%;
    }
  }

  &__pins {
    .pin {

      &__icon {
        position: absolute;
        align-items: center;
        background: rgba(white, 0.85);
        border-radius: 50%;
        cursor: pointer;
        display: flex;
        justify-content: center;
        width: 53px;
        height: 53px;

        @media (max-width: 599px) {
          width: 45px;
          height: 45px;
        }

        &::before {
          background: $color--black;
          border-radius: 50%;
          content: "";
          display: block;
          pointer-events: none;
          transition: all 0.3s $cubic;
          width: 20px;
          height: 20px;

          @media (max-width: 599px) {
            width: 18px;
            height: 18px;
          }
        }
        &:hover{
        &::before {
          background: #BC5700;
        }
        }
      }
      &-product {
        position: absolute;
        width: 447px;
        top: 50% !important;
        left: 50% !important;
        right: inherit !important;
        transform: translate(-50%, -50%) !important;
        z-index: 10003;
        @media (max-width: 1360px) {
          width: 380px;
        }
        @media (max-width: 850px) {
          max-height: calc(100% - 40px);
          overflow: auto;
          position: fixed;
          width: calc(100% - 40px);
        }

        @media (min-width: 1025px) {
          display: flex;
          width: unset;
          min-width: 696px;
          .pin-product__image{
            height: auto;
            width: 328px;
          }
          .pin-product__data{
            width: 368px;
            text-align: left;
            padding: 33px;
            .rating{
              margin-left: 0;
            }
          }
        }

          &--active {
            opacity: 1;
            transform: scale(1);
            visibility: visible;
          }
      }

      &__close {
        color: #bc5700;
        cursor: pointer;
        font-size: 17px;
        line-height: 1;
        position: absolute;
        top: 16px;
        right: 15px;
        z-index: 1;
      }

      &--3 {
        .pin__icon{
          top: 261px;
          left: 31%;
          @media (max-width: 1200px) {
                top: 322px;
                left: 20%;
            }
            @media (max-width: 1040px) {
                top: 305px;
                left: 25%;
            }
            @media (max-width: 905px) {
                top: 255px;
                left: 27%;
            }
            @media (max-width: 820px) {
              top: 43%;
              left: 29%;
            }
            @media (max-width: 649px) {
              top: 49%;
              left: 20%;
            }
          }
      }

      &--2 {
        .pin__icon{
         top: 194px;
          right: 42%;

          @media (max-width: 649px) {
            top: 40%;
            right: 31%;
          }
        }
      }

      &--1 {
        .pin__icon{
           bottom: 11%;
           left: 41%;
        }

      }

      &:hover {
        &::before {
          background: $color--brown;
          transform: scale(0.9);
        }
      }

    }
  }

  &::after {
    background: rgba(black, 0.5);
    content: "";
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    transition: all 0.4s $cubic;
    visibility: hidden;
    width: 100%;
    height: 100%;
    z-index: 10002;
    @media (max-width: 1024px) {
      position: fixed;
    }
  }

  &--overlay {
    &::after {
      opacity: 1;
      visibility: visible;
    }
  }
}

.pin-product {
  opacity: 0;
  transform: scale(0);
  transition: all 0.4s $cubic;
  visibility: hidden;

  &__image {
    overflow: hidden;
    position: relative;
    width: 100%;
   /*  height: 303px; */

    &::after {
      background: rgba(black, 0.2);
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  img {
    object-fit: cover;
    width: 100%;
    height: 100%;
  }
  
  &__data {
    background: white;
    padding: 30px 55px;
    text-align: center;
    @media (max-width: 599px) {
      padding: 30px 25px;
    }
  }

  .rating {
    background: url(stars.svg) left/70px 10px no-repeat;
    margin: 0 auto 8px;
    width: 70px;
    height: 10px;
  }

  &__title {
    font-size: 32px;
    line-height: 1.31;
    letter-spacing: 0;
    margin-bottom: 10px;
  }

  &__subtitle {
    font-family: "Avenir LT Pro";
    font-size: 14px;
    font-weight: 700;
    line-height: 17px;
    letter-spacing: 1.17px;
    margin-bottom: 10px;
    text-transform: uppercase;
  }

  &__copy {
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: 0.35px;
    margin-bottom: 10px;
  }

  &__weight {
    font-size: 14px;
    line-height: 1.71;
    letter-spacing: 0.35px;
    margin-bottom: 16px;
  }

  &__link {
    color: $color--brown;
    font-size: 14px;
    font-weight: 700;
    line-height: 1.71;
    letter-spacing: 0.35px;
    text-decoration: underline;
  }
}