.stone-source-carousel {
  padding-top: 50px;
  padding-bottom: 70px;
}
.stone-source-carousel-container {
  position: relative;
  padding-left: calc((100% - 1240px) / 2);
}
.stone-source-carousel .swiper-container{
  text-align: center;
    display: flex;
    margin-top: 80px;
    min-height: 180px;
    flex-direction: column;
    justify-content: space-between;
}
.stone-source-carousel .swiper-text {
  width: 330px;
  margin: 0 auto;
  font-weight: 400;
}
.stone-source-carousel-container .swiper-wrapper{
  align-items: flex-start;
}
.stone-source-carousel .stone-source-carousel-container .swiper {
  margin-right: calc((-100% / 4) / 2);
  padding-bottom: 35px;
}
.stone-source-carousel .swiper-container img{
  max-width: 50%;
  max-height: 70px;
}
.stone-source-carousel .swiper_btn_flex {
  display: flex;
  width: 100px;
  justify-content: space-between;
  position: absolute;
  bottom: 12px;
  right: 20%;
}
.stone-source-carousel .swiper_btn_flex > * {
  -webkit-tap-highlight-color: transparent;
}
.stone-source-carousel .swiper_btn_flex .swiper-button-next svg {
  width: 40px;
}
.stone-source-carousel .swiper_btn_flex .swiper-button-prev svg {
  transform: rotate(180deg);
}
.stone-source-carousel .swiper-button-next:after{
  display: none;
}
.stone-source-carousel .swiper-button-prev:after{
  display: none;
}
@media only screen and (max-width: 600px) {
  .stone-source-carousel .swiper-container{
    text-align: center;
      display: block;
     
      min-height: auto;
     
  }
  .stone-source-carousel .stone-source-carousel-container {
    padding-left: 0;
  }
  .stone-source-carousel .stone-source-carousel-container .swiper {
    margin-right: unset;
  }
  .stone-source-carousel .swiper_btn_flex {
    right: 0;
    left: 0;
    margin: 0 auto;
  }
  .stone-source-carousel {
    padding-top: 0;
    padding-bottom: 75px;
  }
}