.multi-carousel {
  padding-top: 70px;
  padding-bottom: 100px;
}
.swiper-wrapper {
  align-items: center;
}
.multi-carousel-container {
  max-width: 1240px;
  margin: 0 auto;
}
.multi-carousel-flex {
  display: flex;
}
.multi-carousel-flex > * {
  width: 50%;
}
.multi-carousel-remote {
  display: flex;
  align-items: center;
  padding: 20px;
}
.multi-carousel-remote-control {
  max-width: 100%;
  margin-top: -15vw;
}
.multi-carousel-remote-headline {
  margin-bottom: 0;
  letter-spacing: normal;
}
.multi-carousel-remote-text {
  padding-bottom: 20px;
}
.multi-carousel-remote-text p {
  max-width: 95%;
}
.arrow-keeper {
  position: relative;
}
.multi-carousel-remote-buttons {
  max-width: calc(100% - 50px);
  overflow: hidden;
}
.multi-carousel-remote-buttons .swiper {
  width: calc(100% + 50px);
}
.multi-carousel-remote-buttons .swiper-slide {
  opacity: .5;
}
.multi-carousel-remote-buttons .swiper-slide.mc-slide-active {
  opacity: 1;
}
.multi-carousel-remote-link {
  color: #af5e1b;
  border-bottom: 1px solid #af5e1b;
  font-weight: bold;
  display: inline-block;
  margin-top: 20px;
  font-size: 14px;
  letter-spacing: 1.17px;
  line-height: 17px;
}
.multi-carousel-remote-link:hover {
  border-bottom-color: transparent;
}
.bigSlide {
  position: relative;
  padding-bottom: 10vw;
}
.bigSlide-image {
  max-width: 80%;
  margin-left: auto;
}
.bigSlide-content {
  background: #3d3d38;
  position: absolute;
  margin-top: -100px;
  padding: 40px 20px;
}
.bigSlide-content,
.bigSlide-content h5,
.bigSlide-content a {
  color: #fff;
}
.bigSlide-content h5 {
  font-size: 32px;
  letter-spacing: normal;
  line-height: 44px;
}
.bigSlide-content a {
  border-bottom: 1px solid #fff;
  display: inline-block;
  margin-top: 10px;
  font-weight: bold;
  font-size: 14px;
}
.bigSlide-content a:hover {
  border-bottom-color: transparent;
}
.bigSlide-content strong {
  font-size: 14px;
  letter-spacing: 0.35px;
  font-weight: 900;
}
.remote-image {
  cursor: pointer;
}
.multi-carousel .swiper-button-next {
  height: 100%;
  right: 0;
  top: 22px;
  width: 50px;
  color: #af5e1b;
  padding-left: 20px;
}
.swiper-button-next svg {
  width: 65px;
}
.swiper-button-next.swiper-button-disabled {
  opacity: 1 !important;
}
.multi-carousel .swiper-button-next:after {
  content: "";
  height: 100%;
  width: 100%;
}

.multi-carousel-remote-buttons .swiper-slide{
  height: 75px;
  max-width: 75px;
}
.remote-image{
  height: 100%;
    width: 100%;
    object-fit: cover;
}
@media only screen and (max-width: 1280px) {
  .multi-carousel-container {
    padding-left: 20px;
    padding-right: 20px;
  }
  .bigSlide {
    padding-bottom: 155px;
  }
  .bigSlide-content {
    margin-top: -10vw;
  }
}

@media only screen and (max-width: 876px) {
  .multi-carousel-remote-headline {
    font-size: 32px;
    line-height: 44px;
  }
 
  .arrow-keeper {
    position: initial;
  }
  .multi-carousel-remote-buttons .swiper {
    width: unset;
  }
  .multi-carousel-remote-text {
    padding-bottom: 0;
  }
  .multi-carousel-flex {
    flex-wrap: wrap;
    position: relative;
    padding-bottom: 18vw;
  }
  .multi-carousel-flex > * {
    width: 100%;
  }
  .multi-carousel-remote {
    align-items: flex-end;
  }
  .multi-carousel-remote {
    padding-left: 0;
    padding-right: 0;
  }
  .multi-carousel-remote-buttons {
    position: absolute;
    bottom: -5vw;
    max-width: calc(100% + 45px);
    left: unset;
    right: -45px;
    width: 100%;
  }
  .bigSlide-image {
    max-width: 100%;
  }
  .bigSlide-content {
    position: initial;
    margin-top: 0;
  }
  .bigSlide {
    padding-bottom: 0;
  }
  .multi-carousel .swiper-button-next {
    display: none;
    padding-left: 3vw;
    width: 8vw;
  }
}
.multi-carousel_meet_url{
  border-bottom: 2px solid #AF5D00;
    display: inline-block;
    margin-top: 32px;
    font-weight: 800;
    font-size: 14px;
    color: #AF5D00;
}
.multi-carousel_meet_url:hover{
  border-bottom: 2px solid transparent;
}
@media only screen and (max-width: 450px) {
  .multi-carousel-remote-buttons {
    bottom: -10vw;
  }
  .multi-carousel_meet_url{
  
     
      margin-top: 24px;
     
      margin-bottom: 40px;
      color: #AF5D00;
  }
}