.clinical-results {
  &__container {
    @include make-container;
  }

  &__row {
    @include make-row;

    align-items: center;
  }

  &__text {
    flex: 0 0 46.8%;
    padding-right: 90px;

    @media (max-width: 1024px) {
      flex: 0 0 100%;  
      padding-right: 0;
    }
  }

  &__title {
    font-size: 40px;
    line-height: 1.35;
    letter-spacing: 0;
    margin-bottom: 10px;

    @media (max-width: 1024px) {
      font-size: 32px;  
    }
  }

  &__copy {
    font-size: 18px;
    font-weight: 400;
    letter-spacing: 0.45px;
    line-height: 1.72;
    margin-bottom: 20px;
  }

  &__list {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(3, 1fr);
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;

    @media (max-width: 1024px) {
      margin-bottom: 30px; 
    }

    @media (max-width: 599px) {
      grid-template-columns: repeat(1, 1fr); 
    }

    li {
      align-items: center;
      background: $color--gray;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 15px 10px;
      text-align: center;
    }

    h3 {
      color: $color--brown;
      font-size: 30px;
      font-weight: 500;
      line-height: 1.35;
      margin-bottom: 0;
    }

    p {
      color: $color--black;
      font-size: 14px;
      font-weight: 400;
      line-height: 1.4;
      letter-spacing: 0.45px;
    }
  }

  &__slider {
    flex: 1;
    min-width: 0;
    overflow: hidden;
    position: relative;
    padding-bottom: 35px;

    @media (max-width: 1024px) {
      flex: 0 0 100%;  
    }

    &-track {
      box-sizing: content-box;
      display: flex;
      transition-property: transform;
      position: relative;
      width: 100%;
      height: 100%;
      z-index: 1;
    }

    &-item {
      flex-shrink: 0;
      position: relative;
      width: 100%;

      @media (max-width: 1024px) {
        max-width: 100%;
      }
    }
  }

  &__pagination {
    bottom: 0 !important;
    display: flex;
    justify-content: center;
    text-align: center !important;
    height: 8px;
    width: 100% !important;

    .swiper-pagination-bullet {
      background: #AF5D00 !important;
      opacity: 1 !important;

      &-active {
        opacity: 0.5 !important;
      }
    }
  }
}