.featured-product {
  &__container {
    @include make-container;

    color: $color--black;
    padding-top: 100px;
    padding-bottom: 60px;

    @media (max-width: 599px) {
      padding-top: 50px;
      padding-bottom: 50px;
    }
  }

  &__row {
    @include make-row;

    &--first {
      justify-content: center;
      margin-bottom: 47px;
    }

    &--second {
      align-items: center;
      justify-content: center;
    }
  }

  &__text-col {
    flex: 1;

    @media (max-width: 730px) {
      margin-bottom: 40px;
      text-align: center;
    }
  }

  &__main-title {
    font-size: 40px;
    line-height: 54px;
    margin-bottom: 0;
    text-align: center;
  }

  &__subtitle {
    font-weight: 700;
    letter-spacing: 0.8px;
    margin-bottom: 5px;
    text-transform: uppercase;
  }

  &__copy {
    font-weight: 400;
    letter-spacing: 0.4px;
    margin-bottom: 20px;
  }

  &__title {
    font-size: 40px;
    line-height: 54px;
  }

  &__images {
    flex: 0 0 440px;
    padding: 0 20px;
    position: relative;
    text-align: center;

    @media (max-width: 1220px) {
      flex: 0 0 380px;
    }
  }

  &__image {
    margin-left: 30px;
    width: 175px;

    @media (max-width: 1220px) {
      width: 140px;
    }
  }

  &__texture {
    position: absolute;
    top: 70px;
    left: 20px;
    width: calc(100% - 40px);
    z-index: -1;
  }

  &__features {
    flex: 1;
    padding-left: 36px;
    text-align: center;

    @media (max-width: 1220px) {
      padding-left: 20px;
    }

    @media (max-width: 599px) {
      padding-left: 0;
    }
  }

  &__link {
    color: $color--brown;
    font-size: 14px;
    font-weight: 700;
    line-height: 17px;
    letter-spacing: 1.17px;
    text-decoration: underline;
    text-transform: uppercase;
  }
}

.features-list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 70px 30px;
  margin-bottom: 70px;

  @media (max-width: 1220px) {
    gap: 70px 20px;
  }

  &__item {
    align-items: center;
    display: flex;
    flex-direction: column;

    img {
      margin-bottom: 18px;
      height: 40px;
    }

    p {
      color: $color--dark;
      font-size: 14px;
      font-weight: 700;
      line-height: 17px;
      letter-spacing: 1.17px;
      text-transform: uppercase;
      white-space: nowrap;

      @media (max-width: 1220px) {
        font-size: 13px;
        letter-spacing: 0.8px;
        max-width: 100px;
      }
    }
  }
}
.featured-product__main-title{
  letter-spacing: normal;
}
@media (max-width: 900px) {
 .features-list__item p{
   white-space: normal;
   max-width: 100px;
   font-size: 14px;
    letter-spacing: 1.17px;
    line-height: 16px;
 }
 .features-list{
   gap: 50px 0px;
   margin-left: auto;
   margin-right: auto;
 }
 .featured-product__main-title{
   font-size: 32px;
   line-height: 44px;
 }
 .featured-product__title, .featured-product__subtitle, .featured-product__copy{
   text-align: left;
 }
 .featured-product__title{
   font-size: 36px;
   line-height: 48px;
   letter-spacing: normal;
 }
 .featured-product__subtitle{
   font-size: 14px;
   letter-spacing: 0.7px;
 }
}
#shopify-section-ambassador-story-video{
  outline-color: transparent;
}