.banner {
  background: $color--black;
  color: white;
  overflow: hidden;
  padding: 30px 0;
  position: relative;
  white-space: nowrap;
  width: 100%;
  
  &__container {
    display: flex;
    flex-wrap: nowrap;
  }

  &__track {
    align-items: center;
    
    display: flex;
    flex: 0 0 100vw;
    justify-content: space-between;

    @media (max-width: 1023px) {
      flex: 0 0 calc(170px * var(--cols));  
    }
  }

  &__item {
    align-items: center;
    display: flex;
    flex-direction: column;
    flex: 0 0 calc(100vw / var(--cols));

    @media (max-width: 1023px) {
      flex: 0 0 170px;  
    }

    &-img {
      margin-bottom: 15px;
      width: 40px;
      height: 40px;
    }

    &-title {
      font-size: 14px;
      font-weight: 700;
      line-height: 17px;
      letter-spacing: 1.17px;
      text-transform: uppercase;

      @media (max-width: 1024px) {
        font-size: 12px;        
      }
    }
  }
}
@media (max-width: 768px) {
         .banner__item{
           max-width: 165px;
         }
         .banner__container{
           width: 165px;
           overflow: visible !important;
         }
}
@keyframes scrollText {
  from {
    -webkit-transform: translateX(0);
    transform: translateX(0);
  }
  to {
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
}