.ResultsPage_pr-video-wrap {
    position: relative;
    overflow: hidden;
    width: 100%; 
    padding-top: 29.25%;
  }
  .ResultsPage_pr-video-wrap iframe {
    position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  max-height: 337px;
  }
  .ResultsPage_pr-wrapper{
      margin: auto;
      margin-top: 100px;
      background-color: #3D3D38;
  }
  .ResultsPage_pr-subwrapper{
      max-width: 1201px;
      margin: auto;
      padding-top: 101px;
      margin-bottom: 60px;
      display: flex;
      justify-content: space-between;
      align-items: center;
  }
  .ResultsPage_pr-text-wrapper{
      max-width: 555px;
      margin-right: 42px;
  }
  .ResultsPage_pr-subheading{
    color: #FFFFFF;
    font-family: "Avenir LT Pro";
    font-size: 16px;
    letter-spacing: 0.8px;
    margin-bottom: 5px;
    line-height: 26px;
    font-weight: 600;

  }
  .ResultsPage_pr-heading{
    color: #FFFFFF;
    font-family: "Adobe Garamond Pro";
    font-size: 40px;
    letter-spacing: 0;
    line-height: 54px;
    margin-bottom: 20px;
  }
  .ResultsPage_pr-text{
    color: #FFFFFF;
  font-family: "Avenir LT Pro 55 Roman";
  font-size: 16px;
  letter-spacing: 0.4px;
  line-height: 26px;  
  }
  .ResultsPage_pr-icons-wrap{
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: auto;
      max-width: 1201px;
      padding: 60px 0;
      padding-top: 0;
  }
  .ResultsPage_pr-icon{
      text-align: center;
  }
  .ResultsPage_pr-heading-icon{
    color: #FFFFFF;
    font-family: "Avenir LT Pro";
    font-size: 14px;
    letter-spacing: 1px;
    line-height: 14px;
    font-weight: 600;
  }
  .ResultsPage_pr-subheading{
    text-transform: uppercase;
  }
  .ResultsPage_pr-heading-icon{
    text-transform: uppercase;
  }
  .ResultsPage_pr-icon img{
    max-width: 40px;
    margin-bottom: 16px;
  }
  @media (max-width:1024px){
    .ResultsPage_pr-text-wrapper{
      margin-left: auto;
      margin-right: auto;
  }
    .ResultsPage_pr-wrapper{
      margin: auto;
    }
    .ResultsPage_pr-subwrapper{
      padding: 61px 26px 0px 26px;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      margin-bottom: 0;
    }
    .ResultsPage_pr-subheading{
      font-size: 14px;
      letter-spacing: 0.7px;
      margin-bottom: 0;
      line-height: 26px;
    }
    .ResultsPage_pr-heading{
      font-size: 32px;
  letter-spacing: 0;
  line-height: 44px;
  margin-bottom: 16px;
    }
    .ResultsPage_pr-text{
      font-size: 16px;
  letter-spacing: 0.4px;
  line-height: 26px;
  margin-bottom: 30px;
    }
    .ResultsPage_pr-icons-wrap{
      padding: 55px 0 65px 0;
    }
    .ResultsPage_pr-video-wrap{
      padding-top: 56.25%;
    }
    .ResultsPage_pr-heading-icon{
      font-size: 12px;
      letter-spacing: 1px;
      line-height: 14px;

    }
  }