.quiz-banner {
  position: relative;

  &__container {
    @include make-container;

    align-items: center;
    display: flex;
    justify-content: center;
    position: relative;
    text-align: center;
    height: 500px;

    @media (max-width: 1024px) {
      height: 420px;  
    }
  }

  picture img {
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__text {
    color: white;
    max-width: 810px;
  }

  &__title {
    color: white;
    font-size: 54px;
    line-height: 1.11;
    letter-spacing: 0;
    margin-bottom: 10px;

    @media (max-width: 1024px) {
      font-size: 42px;  
    }
  }

  &__copy {
    font-family: 'Adobe Garamond Pro';
    font-size: 32px;
    font-weight: 400;
    line-height: 1.31;
    letter-spacing: 0;

    @media (max-width: 1024px) {
      font-size: 28px;  
    }
  }

  .btn {
    font-size: 16px;
    letter-spacing: 2px;
    margin-top: 25px;
    width: 333px;

    @media (max-width: 599px) {
      margin-top: 15px;
      width: 100%;
    }
  }
}