.FaqAccordion--heading{
  font-family: 'Ubuntu', sans-serif;
  text-align: center;
  line-height: 52px;
  margin-left: auto;
  margin-right: auto;
  max-width: 100px;
  height: 52px;
  font-size: 40px;
  font-weight: bold;
}
.FaqAccordion__question{
  background-color:rgba(182,171,145,0.15);
  border-radius: 8px;
  margin-bottom: 20px;
}
button.FaqAccordion__button.FaqAccordion--vertical-align{
  background-color:rgba(182,171,145,0.15) ;
  border-radius: 8px;
}
button.FaqAccordion__button.FaqAccordion--vertical-align.is--open{
  background-color: #80894F;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
}
.FaqAccordion--vertical-align{
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.FaqAccordion__button {
  width: 100%;
  font-family: 'Ubuntu', sans-serif;
  border: none;
  letter-spacing: 0;
  line-height: 30px;
  color: #505D68;
  outline: none;
  background-color: white;
  text-align: left;
  padding:25px 0;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.2s linear;
}

.FaqAccordion__button:after {
  content: "+";
  font-size: 35px;
  font-weight: 500;
  transform: translate(-1px);
  color:  #3D3D38;
}
.FaqAccordion__button.is--open:after {
  content: "-";
  font-size: 35px;
  font-weight: 500;
  transform: translate(-3px,-4px);
  color: white;
}
.FaqAccordion__button.is--open {
  padding-bottom: 20px;
}
.FaqAccordion--content-style{
  margin-top: 0;
  max-width: calc(100% - 43px);
  letter-spacing: 0;
  margin-bottom: 25px;
  color: #A1AEB7;
  font-family: 'Ubuntu', sans-serif;
  font-size: 14px;
  line-height: 26px;
}
.FaqAccordion__content {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-in-out;
}
