.ResultsPage_jts-wrapper{
    max-width: 1205px;
    margin: 50px auto;
}
.ResultsPage_jts-subwrapper{
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: 566px;
}
.ResultsPage_jts-heading{
  color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 12px;
  letter-spacing: 1px;
  line-height: 14px;
  font-weight: 600;
}
.ResultsPage_jts-link{
  color: #222222;
  font-family: "Avenir LT Pro";
  font-size: 12px;
  letter-spacing: 1px;
  line-height: 14px;
  font-weight: 600;
  text-transform: uppercase;
  text-align: center;
}
.ResultsPage_jts-link:hover{
    text-decoration: underline;
}
.ResultsPage_jts-link{
  cursor: pointer;
}
@media (max-width:1024px){
  .ResultsPage_jts-wrapper{
    display: none;
  }
}