.copy-with-bg {
  position: relative;

  &__container {
    @include make-container;

    align-items: center;
    display: flex;
    position: relative;
    height: 500px;

    @media (max-width: 599px) {
      align-items: flex-start;
      padding-top: 40px;
    }
  }

  picture img {
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  &__text {
    max-width: 480px;
  }

  &__title {
    font-size: 48px;
    line-height: 1.2;
    letter-spacing: 0;
    margin-bottom: 20px;

    @media (max-width: 599px) {
      margin-bottom: 10px;
    }
  }

  &__copy {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.72;
    letter-spacing: 0.45px;
  }

  .btn {
    font-size: 16px;
    letter-spacing: 2px;
    margin-top: 20px;
    width: 290px;

    @media (max-width: 599px) {
      margin-top: 15px;
      width: 100%;
    }
  }
}