/* Override header for templates*/
body.template__page--ambassador-landing,
body.template__gift_card,
body.template__product--gift-card,
body.template__page--gift_card{
    .header{
        .promo-bar {
            background-color: #3d3d38;
        }
    }
    .nav-desktop{
        border-bottom: 1px solid #ececec;
    }
    @media (min-width: 1024px){
        .header{
            #shopify-section-mega-menu {
              transition: background-color .4s;
              background: #fff;
            }
          }
          .nav-desktop__account-link,.nav-desktop__link{
            color: #3d3d38;
          }
          .nav-desktop__cart-trigger .icon,.nav-desktop__logo .icon{
            fill: #3d3d38;
        }
    }
    .main-content{
      padding:113px 0 0 0;
        &.promo-bar--removed{
          padding:59px 0 0 0;
        }
      @media screen and (min-width: 48rem){
        padding:133px 0 0 0;
          &.promo-bar--removed{
            padding:80px 0 0 0;
          }
      }
      @media screen and (min-width: 64rem){
        padding:122px 0 0 0;
        &.promo-bar--removed{
          padding:71px 0 0 0;
        }
      }
    }
    .header #shopify-section-mega-menu{
    background: #fff;
    }
    .main-content{
    background: #f7f6f3;
    }
}

body.template__page--ambassador-landing{
    .main-content{
        padding-top: 0;
    }
     .hero-home-ambassador{
        margin-top: 0 !important;
     }
}
/* Checkout gift card layout */
.gift-card-wrapper{
  padding: 120px 20px;
  max-width: 1182px;
  text-align: center;
    @media screen and (min-width: 768px){
        text-align: left;
        display: flex;
        align-items: center;
    }
}
@media screen and (max-width: 767px){
    .gift-card-right{
        margin-top: 45px;
    }
}
.gift-card__heading{
  font-size: 54px;
  line-height: 1.11;
  max-width: 80%;
  margin-bottom: 30px;
    @media screen and (max-width: 767px){
       margin: 0 auto; 
       font-size: 42px;
       line-height: 1.29;

    }
}
.gift-card__image{
    width: 100%;
    height: auto;
}
.gift-card__price{
    font-size: 18px;
    line-height: 1.72;
}
.gift-card__information{
    display: flex;
    margin: 45px 0 0 0;
    align-items: center;
    @media screen and (max-width: 767px){
        display:block;
    }
}
.gift-card__qr-code{
    margin-top: 15px;
}
input[type=text].gift-card__number{
    max-width: 275px;    
    border: solid 0.5px #d4cdbd;
    border-radius: 4px;
    height: 32px;
    font-size: 14px;
    line-height: 1.71;
    color: #3d3d3d;
    padding-top: 4px;
    @media screen and (max-width: 767px){
        width: 100%;
        max-width: unset;
    }
}

.gift-card__copy-link,
.gift-card__buttons button{
    background: none;
    border: none;
    box-shadow: none;
    cursor: pointer;
    text-decoration: underline;
    color:#af5d00;
    font-size: 14px;
    font-weight: 900;
    line-height: 1.57;
    margin-left: 15px;
    &:hover{
        color: #3d3d3d;
    }
}
.gift-card__copy-link{
    margin-left: 30px;
}
.gift-card-btn{
    padding: 14px 38px 11px;
    font-size: 14px;
    letter-spacing: 1.68px;
    margin-top: 45px;
}
@media screen and (max-width: 767px){
    .gift-card__qr-code{
        img{
            margin: 0 auto;
        }
    }
    .gift-card__copy-code, .gift-card__buttons{
        margin-top: 15px;
        display: inline-block;
    }
    .gift-card__copy-link{
        margin-left: 0;
    }
}
body.template__gift_card{
    .nav-desktop__cart-trigger,
    .nav-mobile__icon-link{
        display:none;
    }
}

/* Gift card Product page */
.pdp-regular-template{
    &.pdp-giftcard-template{
        margin-top: 95px;
        @media screen and (min-width: 640px){
            margin-top: 184px;
        }
        @media screen and (min-width: 48rem){
            margin-top: 122px;
        }
        padding: 0;
        .c-productInfo__title {
            font-size: 36px;
            line-height: 48px;
            @media screen and (min-width: 64rem){
                font-size: 44px;
                line-height: 54px;
            }
        }
        .c-product__row{
            padding-bottom: 0;
            /* height: auto !important;  */
        }
        &.promo-bar--removed{
            margin-top: 59px;
            @media screen and (min-width: 640px){
                margin-top: 81px;
            }
            @media screen and (min-width: 48rem){
                margin-top:  71px;
            }
        }
        .c-productInfo__variants{
            label{
                font-size: 14px;
                font-weight: 900;
                letter-spacing: 1.17px;
                padding-bottom: 4px;
                display: block;
            }
        }
        .c-productInfo__variantSelect{
            display: block;
            border-color:#3d3d3d;
            border-radius: 0;
            font-size: 16px;
            .option{
                background-color: #fff;
            }
        }
        .c-productInfo__currentVariant{
            margin-bottom: 0;
            padding-bottom: 0.2rem;
        }
        .c-productInfo__col{
            &:not(.sticky){
                background: #fff;
                height: 100%;
                @media (max-width: 1023px){               
                    max-width: 100%;
                }
                position: relative;
                &:after{
                    position: absolute;
                    content: '';
                    height: 100%;
                    width: 30vw;
                    display: block;
                    background: #fff;
                    left: 100%;
                    top:0;
                }
                .c-productInfo__col--wrapper{
                padding-top: 35px;
                padding-bottom: 20px;
                    @media (min-width: 1024px){     
                        padding: 140px 85px 140px;
                    }
                }
            }
            &.sticky{
                .c-productInfo__variants{
                    label{
                        display: none;
                    }
                }
                .c-productInfo__col--wrapper{
                    align-items: center;
                }
                .ss-main .ss-single-selected{
                    height: 60px;
                    width: 200px;
                    margin-right: 15px;
                    background-color: #fff;
                }
                .submit-message, .customize-btn{
                    display: none;
                }
                #add-item-form{
                    padding: 10px 0;
                }
                @media (max-width: 991px){
                    padding-top: 25px;
                    .ss-main .ss-single-selected{
                        width: 100%;
                        height: 50px;
                        min-width: 300px;
                   }
                   .c-productInfo__submit{
                        width: 60%;
                        margin-left: 10px;                        
                        .btn{
                            width: 100% !important;
                       }
                    }
                   #add-item-form{
                        padding-bottom: 0;
                        display: flex;
                    }
                }
                 @media (max-width: 600px){
                    .ss-main .ss-single-selected{
                        min-width: 250px;
                    }
                 }
                 @media (max-width: 480px){
                    .ss-main .ss-single-selected{
                        min-width: 100px;
                    }
                 }
            }
        }
        @media (max-width: 1023px){   
            .c-productInfo__description p{
                font-size: 16px;
                line-height: 1.5;
            }
        }
    }
}
body.template__product--gift-card{
    .main-content {
        padding: 0;
    }
    .acsb-trigger.acsb-trigger-position-x-left.acsb-mobile {
        bottom: 90px!important;
    }
     @media (max-width: 991px){
      &.show-sticky #chat-button {
            bottom: 60px!important;
        }  
     }
}