.hero3 {
  &__slider {
    position: relative;
    
    picture {
      @media (max-width: 1023px) {
        width: 100%;
      }
    }
  }

  &__track {
    box-sizing: content-box;
    display: flex;
    transition-property: transform;
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;

    @media (max-width: 1023px) {
      align-items: flex-start;
    }
  }

  &__slide {
    align-items: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-shrink: 0;
    position: relative;
    width: 100%;
    height: 700px;

    @media (max-width: 1023px) {
      flex-direction: column;
      height: initial;
    }

    &.swiper-slide-active {
      .hero3__subtitle,
      .hero3__title,
      .hero3__copy,
      .hero3__button-wrapper {
        opacity: 1;
        transform: translateY(0) scaleY(1);
      }
    }
  }

  &__row {
    display: flex;
    width: 100%;
    height: 100%;

    @media (max-width: 1023px) {
      height: initial;
    }
  }

  &__image {
    object-fit: cover;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;

    @media (max-width: 1023px) {
      height: 320px;
      position: relative;
    }

    &-container {
      flex: 1;
      position: relative;

      @media (max-width: 1023px) {
        display: none;
      }
    }
  }

  &__text {
    align-items: flex-start;
    display: flex;
    flex: 0 0 40vw;
    flex-direction: column;
    justify-content: center;
    padding-left: 100px;
    position: relative;

    &--big {
      flex: 0 0 60vw;

      .hero3__title,
      .hero3__copy {
        max-width: 540px;
      }
    }

    @media (max-width: 1023px) {
      align-items: center;
      flex: 1;
      justify-content: flex-start;
      padding: 40px 20px 0;
      text-align: center;

      picture {
        display: none;
      }
    }

    & * {
      color: inherit;
      transition: all 0.6s $cubic;

      @media (max-width: 1023px) {
        color: $color--black;
      }
    }
  }

  &__subtitle {
    font-weight: 700;
    letter-spacing: 0.8px;
    margin-bottom: 10px;
    text-transform: uppercase;
    opacity: 0;
    transform: translateY(40px) scaleY(0);
  }

  &__title {
    font-size: 54px;
    line-height: 60px;
    max-width: 400px;
    opacity: 0;
    transform: translateY(40px) scaleY(0);

    @media (max-width: 1023px) {
      font-size: 42px;
      line-height: 54px;
    }
  }

  &__copy {
    font-weight: 500;
    margin-bottom: 35px;
    max-width: 400px;
    opacity: 0;
    transform: translateY(40px) scaleY(0);
  }

  &__button {
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 1.5px;
    padding: 23px 54px;
  }

  &__button-wrapper {
    align-items: center;
    display: flex;
    opacity: 0;
    transform: translateY(40px) scaleY(0);
  }

  &__link {
    font-size: 14px;
    font-weight: 800;
    line-height: 26px;
    margin-left: 40px;
    position: relative;
    text-transform: uppercase;

    &::after {
      background: white;
      content: "";
      display: block;
      height: 2px;
    }
  }
}

.vimeo-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  pointer-events: none;
  width: 100%;
  height: 100%;
  z-index: -1;

  @media (max-width: 1023px) {
     height: 410px;
    position: relative;
  }
    @media (max-width: 600px) {
     height: 306px;
  }
}

.vimeo-wrapper iframe {
  min-height: 100vh;
  min-width: 177.77vh; /* Given a 16:9 aspect ratio, 16/9*100 = 177.77 */
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100vw;
  height: 56.25vw; /* Given a 16:9 aspect ratio, 9/16*100 = 56.25 */
}
@media (max-width: 1023px) {
    .hero3__slider {
      margin-bottom: 50px;
    }
}
body .swiper-horizontal>.swiper-pagination-bullets, 
body .swiper-pagination-bullets.swiper-pagination-horizontal, 
body .swiper-pagination-custom, 
body .swiper-pagination-fraction {
  position: absolute;
  right: 100px;
  bottom: 60px;
  text-align: right;
  width: auto;

  @media (max-width: 1023px) {
    text-align: center;
    right: 0;
    bottom: -50px;
  }

  & > span {
    background: none;
    color: white;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 1.17px;
    font-weight: 800;
    margin: 0 6px !important;
    opacity: 0.6 !important;

    @media (max-width: 1023px) {
      color: #3D3D38;
    }

    &.swiper-pagination-bullet-active {
      opacity: 1 !important;
    }
  }
}
.swiper-rtl .swiper-button-next, .swiper-rtl .swiper-button-prev{
 display: none;
