/* Video */

 .video-responsive {
  background: #000;
  overflow: hidden;
  padding-bottom: 56.25%;
  position: relative;
  height: 0; 
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  position: absolute; }

.vimeo-mob{
  display: none;
  background: #000;
}
.vimeo-mob-square iframe {
  left: 50%;
  top: 50%;
  height: 100vw;
  width: 100%;
  position: absolute;
  transform: translate(-50%, -50%);
}
@media (max-width:480px) {
  .vimeo-desktop{
    display: none;
  }
  .vimeo-mob{
    display: block;
  }
 .video-responsive-mob {
    background: #000;
    overflow: hidden;
    padding-bottom: 48.25%;
    position: relative;
    height: 0; 
  }

  .video-responsive-mob iframe {
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    position: absolute; 
  }
}
